import { Component, Input, OnDestroy, OnInit, SimpleChanges, ViewEncapsulation } from '@angular/core';
import { TDLService } from '../../services/tdl.service';
import { saveAs } from 'file-saver';
import { Subscription } from 'rxjs';
import { TDLBurndownHrs } from '../../models/tdl-burndown.model';
import { TDL } from '../../models/tdl.model';
import { ResourceService } from '../../services/resource.service';

declare var jQuery: any;

@Component({
  selector: '[burn-down]',
  templateUrl: "./burndown-chart.template.html",
  providers: [ResourceService],
  encapsulation: ViewEncapsulation.None
})
export class BurndownChartComponent implements OnInit, OnDestroy {

  @Input() tdlID: number;
  @Input() tdlModel: TDL;

  public showErrorFlag: boolean;
  public alerts: Array<Object>;
  public switchFilter: number = 1;

  public tdlburndownHours: TDLBurndownHrs[] = [];

  public busyLoadResources: Subscription;

  constructor(
    private resourceService: ResourceService,
    private tdlService: TDLService) {
      this.alerts = [
        {
          type: 'warning',
          msg: '<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
        }
      ];
      this.showErrorFlag = false;
  }

  ngOnInit() {
    this.loadTDLBurndownHoursByWeeks();
  }

  ngOnDestroy() {
    if (this.busyLoadResources) {
      this.busyLoadResources.unsubscribe();
    }
  }

  public loadTDLBurndownHoursByWeeks(){
    let handler = this;
    this.busyLoadResources = this.tdlService.getTDLBurndownHoursByWeeks(this.tdlID).subscribe(
      data => {
        if (data) {
          handler.tdlburndownHours = data;
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  ngOnChanges(changes: SimpleChanges){
  }

  // events
  public chartClicked(e: any): void {
  }

  public chartHovered(e: any): void {
  }

  public downloadGraph() {
    let canvas = jQuery("#cht_burndown").get(0) as HTMLCanvasElement;
    canvas.toBlob(function (blob) {
      saveAs(blob, "burndown.png");
    });
  }

  public downloadPlanvsActualGraph() {
   let canvas = jQuery("#cht_plnvsact").get(0) as HTMLCanvasElement;
   canvas.toBlob(function (blob) {
     saveAs(blob, "planvsact.png");
   });
  }

  reset(flag) {
     // this.resetChartData();
     jQuery("#burn-down").modal("hide");
  }
}