import { TDL } from './tdl.model';

export class TDLFilter {
    clientFilter: string;
    tdlFilter: string;
    wbsCodeFilter: string;
    pmFilter: string;
    plnStartFilter: string;
    plnEndFilter: string;
    statusFilter: string;
    categoryFilter: string;
    tdlTypeFilter: string;
    clinCodeFilter: string;
    plnDateRangeStartFilter: string;
    plnDateRangeEndFilter: string;
    results: TDL[];
}

export class TDLFilterData {
    private _clientFilter: string;
    private _tdlFilter: string;
    private _wbsCodeFilter: string;
    private _pmFilter: string;
    private _plnStartFilter: string;
    private _plnEndFilter: string;
    private _statusFilter: string;
    private _categoryFilter: string;
    private _tdlTypeFilter: string;
    private _clinCodeFilter: string;
    private _plnDateRangeStartFilter: string;
    private _plnDateRangeEndFilter: string;

    get clientFilter(): any {
        return this._clientFilter;
    }
    set clientFilter(value: any) {
        this._clientFilter = value;
    }

    get tdlFilter(): any {
        return this._tdlFilter;
    }
    set tdlFilter(value: any) {
        this._tdlFilter = value;
    }

    get wbsCodeFilter(): any {
        return this._wbsCodeFilter;
    }
    set wbsCodeFilter(value: any) {
        this._wbsCodeFilter = value;
    }

    get pmFilter(): any {
        return this._pmFilter;
    }
    set pmFilter(value: any) {
        this._pmFilter = value;
    }

    get plnStartFilter(): any {
        return this._plnStartFilter;
    }
    set plnStartFilter(value: any) {
        this._plnStartFilter = value;
    }

    get plnEndFilter(): any {
        return this._plnEndFilter;
    }
    set plnEndFilter(value: any) {
        this._plnEndFilter = value;
    }

    get statusFilter(): any {
        return this._statusFilter;
    }
    set statusFilter(value: any) {
        this._statusFilter = value;
    }

    get clinCodeFilter(): any {
        return this._clinCodeFilter;
    }
    set clinCodeFilter(value: any) {
        this._clinCodeFilter = value;
    }

    get plnDateRangeStartFilter(): any {
        return this._plnDateRangeStartFilter;
    }
    set plnDateRangeStartFilter(value: any) {
        this._plnDateRangeStartFilter = value;
    }

    get plnDateRangeEndFilter(): any {
        return this._plnDateRangeEndFilter;
    }
    set plnDateRangeEndFilter(value: any) {
        this._plnDateRangeEndFilter = value;
    }

    get categoryFilter(): any{
        return this._categoryFilter;
    }

    set categoryFilter(value: any){
        this._categoryFilter = value;
    }

    get tdlTypeFilter(): any{
        return this._tdlTypeFilter;
    }

    set tdlTypeFilter(value: any){
        this._tdlTypeFilter = value;
    }
 }
