	public ngOnDestroy(): void {
		if (this.busyLoadNamedResources) {
			this.busyLoadNamedResources.unsubscribe();
		}
		if (this.busyLoadTBDResources) {
			this.busyLoadTBDResources.unsubscribe();
		}
		if (this.busyloadLaborRatePeriods) {
			this.busyloadLaborRatePeriods.unsubscribe();
		}
		if (this.busyloadAllSkills) {
			this.busyloadAllSkills.unsubscribe();
		}
		if (this.busyRecalcTDLHoursAndCost) {
			this.busyRecalcTDLHoursAndCost.unsubscribe();
		}
		if(this.busyAddResourcePlanHours){
			this.busyAddResourcePlanHours.unsubscribe();
		}
		if(this.busyCreateTDLResource){
			this.busyCreateTDLResource.unsubscribe();
		}
		if(this.busyLoadLaborCategories){
			this.busyLoadLaborCategories.unsubscribe();
		}
	}

	public refreshSelectedSkills() {
		if(isNil(this.resource.skills)){
			return;
		}
		let arrSelectedSkills = this.resource.skills.split(",");
		this.selectedskillItems = [];
		for (let ss of arrSelectedSkills) {
			for (let s of this.skillsdropdownList) {
				if (s.id === Number(ss)) {
					let dli: DropDownListItem = new DropDownListItem(s.id, s.itemName, s.itemCode);
					this.selectedskillItems.push(dli);
				}
			}
		}
	}

	ngOnChanges(changes: { [propertyName: string]: SimpleChange }) {
		if(!isNil(changes)){
			// tslint:disable-next-line: forin
			for (let propName in changes) {
				if (propName === "resModel") {
					let chg = changes[propName];
					if (chg.currentValue) {
						this.resource.tdlResourceID = chg.currentValue.tdlResourceID;
						this.resource.tdlID = chg.currentValue.tdlID;
						this.resource.resourceID = chg.currentValue.resourceID;
						this.resource.lcatCode = chg.currentValue.lcatCode;
						this.resource.period1 = chg.currentValue.budgetPeriod1;
						this.resource.period2 = chg.currentValue.budgetPeriod2;
						this.resource.hours = chg.currentValue.budgetHours;
						this.resource.hours2 = chg.currentValue.budgetHours2;
						this.resource.skills = chg.currentValue.skills;
						if (this.resource.tdlResourceID > 0) {
							this.editmode = "Edit";
							jQuery("#res-name").prop("disabled", "disabled");
						}
					}
				}
				if (propName === "TDLId") {
					let chg = changes[propName];
					this.selTDLId = chg.currentValue;
				}
				if(propName === "LCATGroupId"){
					let chg = changes[propName];
					this.selLCATGroupId = chg.currentValue;
					this.loadLaborCategories(this.selLCATGroupId);
				}
			}
		}
	}

	public loadSkills() {
		let handler = this;
		this.skillsdropdownList = [];
		this.busyloadAllSkills = this.skillService.getAllSkills().subscribe(
			data => {
				if (data) {
					handler.listSkills = data;
					for (let s of handler.listSkills) {
						let dli: DropDownListItem = new DropDownListItem(s.skillId, s.skillName, s.skillCode );
						handler.skillsdropdownList.push(dli);
					}
					handler.refreshSelectedSkills();
				}
			},
			error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

