import { Component, OnInit } from '@angular/core';
import * as _ from "lodash";
import { isNil } from 'lodash';
import { IMyDateModel, IMyDpOptions } from "mydatepicker";
import { Subscription } from 'rxjs';
import { DateService } from '../../../shared/service/date.service';
import { TDLFilter, TDLFilterData } from '../../models/tdl-filter.model';
import { TDLPeriodForecastView } from '../../models/tdl-period-forecast.model';
import { TDLService } from '../../services/tdl.service';
import { TDLPlanHoursQuery } from '../../../timesheet/models/planhr-query.model';

declare var jQuery: any;

@Component({
    selector: 'tdl-period-forecast',
    templateUrl: './tdl-period-forecast.template.html',
    styleUrls: ['./tdl-period-forecast.style.scss']
})
export class TDLPeriodForecastComponent implements OnInit {
    public forecastdata: TDLPeriodForecastView[] = [];
    public showErrorFlag: boolean;
    public alerts: Array<Object>;

    public weekEndingModel: any = {};
	public weekEnding: Date;
	public arrWeekstoDisplay: number[] = [];
	public numWeekstoDisplay: number = 7;
	public selReportType: string;
	public busyLoadForecastMatrix: Subscription;

	public sortResultsBy = "name";
	public sortResultsOrder = "asc";
	public pageNumResults: number = 1;

	public tdlfilter: TDLFilter = new TDLFilter();
	public tdlfilterdata: TDLFilterData = new TDLFilterData();

	myDatePickerOptions: IMyDpOptions = {
		dateFormat: "mm/dd/yyyy",
		firstDayOfWeek: "su",
		sunHighlight: false,
		satHighlight: true,
		showClearDateBtn: false
	};

    public columns: Array<any> = [
		{
			name: "name",
			type: "input",
			width: "150",
			filtering: { filterString: "", columnName: "name", placeholder: "" }
		},
		{
			name: "category",
			type: "input",
			width: "150",
			filtering: { filterString: "", columnName: "category", placeholder: "" }
		},
		{
			name: "status",
			type: "input",
			width: "150",
			filtering: { filterString: "", columnName: "status", placeholder: "" }
		}
	];

    constructor(
        private tdlService: TDLService
    ) {
        this.arrWeekstoDisplay = [];
		for (let i = 0; i < this.numWeekstoDisplay; i++) {
			this.arrWeekstoDisplay.push(i);
        }

        this.showErrorFlag = false;
        this.alerts = [
            {
              type: 'warning',
              msg: '<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
            }
          ];
    }

    ngOnInit(): void { }

    ngOnDestroy(): void {
		if (this.busyLoadForecastMatrix) {
			this.busyLoadForecastMatrix.unsubscribe();
		}
	}

    ngAfterViewInit() {
		for (let col of this.columns) {
			if (col.filtering) {
				this.cosmeticFix("#filter-" + col.name);
			}
		}
		this.RefreshPlanSheet();
    }

    public sortFnAvailability() {}

    public sortFnAllocation() {}

    public cosmeticFix(elementID: any) {
		// The code below for cosmetic fix - Blue box now surrounds the icon on the search company text box
		let filterInput = jQuery(elementID);
		filterInput
			.focus(e => {
				jQuery(e.target)
					.closest(".input-group")
					.addClass("focus");
			})
			.focusout(e => {
				jQuery(e.target)
					.closest(".input-group")
					.removeClass("focus");
			});
	}

    public getThisSaturday(): Date {
		return DateService.getThisSaturday();
    }

    public setWeekEndingModel() {
		if (this.weekEnding) {
			this.weekEndingModel = {
				date: {
					year: this.weekEnding.getFullYear(),
					month: this.weekEnding.getMonth() + 1,
					day: this.weekEnding.getDate()
				}
			};
		}
    }

    public getFormattedDate(d: Date): string {
		return `${d.getFullYear()}-${d.getMonth() + 1}-${d.getDate()}`;
    }

    public getPerformancePeriodStartWeekEnd(selDate: Date, src: String) {
		let startWeekEnding: Date = DateService.getFirstSaturdayOfMonthFromGivenDate(
			selDate
        );
		return startWeekEnding;
    }

    public RefreshPlanSheet() {
		this.weekEnding = this.getPerformancePeriodStartWeekEnd(
			this.getThisSaturday(),
			"RefreshPlanSheet"
		);
		this.setWeekEndingModel();
		let startwkEnding = this.getFormattedDate(this.weekEnding);
		this.loadTDLForecastFromDate(startwkEnding);
    }

    public getPerformancePeriodEndWeekEnd(selDate: Date) {
		let endWeekEnding = DateService.getLastSaturdayOfMonthFromGivenDate(
			selDate
		);
		if (
			DateService.getMonthOfDate(endWeekEnding) !==
			DateService.getMonthOfDate(selDate)
		) {
			endWeekEnding = DateService.getDateBySubtractingDaysFromDate(
				endWeekEnding,
				7
			);
		}
		return endWeekEnding;
    }

    public loadTDLForecastFromDate(startWeekEnd) {
		let handler = this;
		let tdq = new TDLPlanHoursQuery();
        tdq.weekEnding = startWeekEnd;
        tdq.popCode = "T01";

		let startweekEnding = this.getPerformancePeriodStartWeekEnd(
			startWeekEnd,
			"loadTDLForecastFromDate"
		);
		let endWeekEnding = this.getPerformancePeriodEndWeekEnd(startWeekEnd);
		this.numWeekstoDisplay = DateService.getDifferenceBetweenDatesinWeeks(
			startweekEnding,
			endWeekEnding
		);
		this.arrWeekstoDisplay = [];
		for (let i = 0; i < this.numWeekstoDisplay; i++) {
			this.arrWeekstoDisplay.push(i);
		}

		if (this.busyLoadForecastMatrix) {
			this.busyLoadForecastMatrix.unsubscribe();
		}

		this.busyLoadForecastMatrix = this.tdlService
			.getTDLForecastMatrixFromDate(tdq)
			.subscribe(
				data => {
					if (data) {
						handler.forecastdata = data;
					}
				},
				error => {
					this.showErrorFlag = true;
					this.alerts = [];
					this.alerts.push({ type: "warning", msg: error });
				}
			);
    }

	public getForecastWeekHour(
		selForeCast: TDLPeriodForecastView,
		index: number,
		reportType: string
	) {
		let weekHours: number[] = [
			selForeCast.wk1,
			selForeCast.wk2,
			selForeCast.wk3,
			selForeCast.wk4,
			selForeCast.wk5,
			selForeCast.wk6,
			selForeCast.wk7
		];
		if (index < 8) {
			if(reportType === 'fte'){
				return weekHours[index]/40;
			}
			return weekHours[index];
		}
		return 0.0;
	}

	public getWeekName(index: number) {
		let startWeekEnding = DateService.getThisSaturday(); // moment().day('Saturday').toDate();
		if (!isNil(this.weekEnding)) {
			startWeekEnding = DateService.getSaturday(this.weekEnding); // moment(this.weekEnding).toDate();
		}
		// return moment(startWeekEnding).day('Saturday').add(index * 7, 'days').format("MM/DD");
		return DateService.getFormattedText(
			DateService.getDateByAddingDaysFromDate(
				DateService.getSaturday(startWeekEnding),
				index * 7
			),
			"MM/DD"
		);
	}

	public gotoPrevDisplayPeriod() {
		if (this.weekEnding) {
			let d: Date = DateService.getDateBySubtractingMonthsFromDate(
				DateService.getStartOfMonthFromGivenDate(this.weekEnding),
				1
			); // moment(this.weekEnding).startOf('month').subtract(1, 'month').toDate();
			this.weekEnding = this.getPerformancePeriodStartWeekEnd(
				d,
				"gotoPrevDisplayPeriod"
			);
		} else {
			this.weekEnding = null;
		}
		this.setWeekEndingModel();
		let wkEnding: any = new Date(
			this.weekEndingModel.date.year,
			this.weekEndingModel.date.month - 1,
			this.weekEndingModel.date.day
		);
		let startwkEnding = this.getFormattedDate(wkEnding);
		this.loadTDLForecastFromDate(startwkEnding);
	}

	public gotoNextDisplayPeriod() {
		if (this.weekEnding) {
			let d: Date = DateService.getDateByAddingMonthsFromDate(
				DateService.getStartOfMonthFromGivenDate(this.weekEnding),
				1
			); // getmoment(this.weekEnding).startOf('month').add(1, 'month').toDate();
			this.weekEnding = this.getPerformancePeriodStartWeekEnd(
				d,
				"gotoNextDisplayPeriod"
			);
		} else {
			this.weekEnding = null;
		}
		this.setWeekEndingModel();
		let wkEnding: any = new Date(
			this.weekEndingModel.date.year,
			this.weekEndingModel.date.month - 1,
			this.weekEndingModel.date.day
		);
		let startwkEnding = this.getFormattedDate(wkEnding);
		this.loadTDLForecastFromDate(startwkEnding);
	}


	public onWeekEndingDateChanged(event: IMyDateModel) {
		if (event.jsdate) {
			const d = event.jsdate;
			this.weekEnding = this.getPerformancePeriodStartWeekEnd(
				d,
				"onWeekEndingDateChanged"
			);
		} else {
			this.weekEnding = null;
		}
		this.setWeekEndingModel();
		let wkEnding: any = new Date(
			this.weekEndingModel.date.year,
			this.weekEndingModel.date.month - 1,
			this.weekEndingModel.date.day
		);
		let startwkEnding = this.getFormattedDate(wkEnding);
		this.loadTDLForecastFromDate(startwkEnding);
	}

	public ApplyFilter() {
		this.refreshTDLFilterObject();
	}

	public refreshTDLFilterObject() {
		this.tdlfilterdata = null;
		this.tdlfilterdata = new TDLFilterData();

		this.tdlfilterdata.tdlFilter = this.tdlfilter.tdlFilter;
		this.tdlfilterdata.categoryFilter = this.tdlfilter.categoryFilter;
		this.tdlfilterdata.statusFilter = this.tdlfilter.statusFilter;
		// this.saveSearchCriteriaToLocalStorage();
	}

	public onChangeTable(filterinfo): void {
		if (filterinfo) {
			if (filterinfo.columnName === "name") {
				this.tdlfilter.tdlFilter = filterinfo.filterString;
				this.tdlfilter.tdlFilter = this.tdlfilter.tdlFilter.trim();
			}
			if (filterinfo.columnName === "category") {
				this.tdlfilter.categoryFilter = filterinfo.filterString;
				this.tdlfilter.categoryFilter = this.tdlfilter.categoryFilter.trim();
			}
			if (filterinfo.columnName === "status") {
				this.tdlfilter.statusFilter = filterinfo.filterString;
				this.tdlfilter.statusFilter = this.tdlfilter.statusFilter.trim();
			}
			if (filterinfo.refresh) {
				this.refreshTDLFilterObject();
			}
		}
	}

	public updateFilterTextBox(colName, colText) {
		let rowIndex = _.findIndex(this.columns, function(o) {
			return o.name && o.name.toString() === colName;
		});
		if (rowIndex !== -1) {
			let selRow = this.columns[rowIndex];
			if (selRow.filtering) {
				selRow.filtering.filterString = colText;
			}
		}
	}

	public onchangeReportType(reportType){
		this.RefreshPlanSheet();
	}

}
