import { TDLFilterData } from '../models/tdl-filter.model';
import * as _ from "lodash";
import { Pipe, PipeTransform } from "@angular/core";
import { isNil } from 'ramda';
import { DateService } from '../../shared/service/date.service';

@Pipe({
    name: "tdlfilterpipe"
})
export class TDLDataFilterPipe implements PipeTransform {


    private getFormattedDate(date): string {
        return DateService.getFormattedText(date, "MMM DD, YYYY"); //moment(date).format("MMM DD, YYYY");
    }

    private getFormattedDateString(value:any){
        if((value != null) && (value.trim().length == 10)){
            if(value.includes('-')){
            return this.getFormattedDate(new Date( value.replace(/-/g, '\/') ));
            }
        }
        return this.getFormattedDate(new Date( value ));
    }

    public transform(data: any[], filter: TDLFilterData): any {
        let results = data;

        if (data) {
            if (filter) {
                let handler = this;
                // filter by tdl name
                if (typeof filter.tdlFilter !== 'undefined' && filter.tdlFilter.length > 0) {
                   results =  _.filter(results, function(item) {
                        let val1:string = isNil(item.name) ? "" : item.name;
                        let val2:string = isNil(filter.tdlFilter) ? "" : filter.tdlFilter;
                        return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                    });
                }
                // filter by client name
                if (typeof filter.clientFilter !== 'undefined' && filter.clientFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                         let val1:string = isNil(item.client) ? "" : item.client;
                         let val2:string = isNil(filter.clientFilter) ? "" : filter.clientFilter;
                         return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                     });
                 }
                // filter by wbs code
                if (typeof filter.wbsCodeFilter !== 'undefined' && filter.wbsCodeFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                            let val1:string = isNil(item.wbsCode) ? "" : item.wbsCode;
                            let val2:string = isNil(filter.wbsCodeFilter) ? "" : filter.wbsCodeFilter;
                            return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                    });
                }
                // filter by pm
                if (typeof filter.pmFilter !== 'undefined' && filter.pmFilter.length > 0) {
                   results =  _.filter(results, function(item) {
                    let val1:string = item.pmdisplay;
                    let val2:string = filter.pmFilter;
                    return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                    });
                }
                // filter by Planned Start
                if (typeof filter.plnStartFilter !== 'undefined' && filter.plnStartFilter.length > 0) {
                   results =  _.filter(results, function(item) {
                        let val1:string = handler.getFormattedDateString(item.planStartDt);
                        let val2:string = filter.plnStartFilter;
                        return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                    });
                }
                // filter by Planned End
                if (typeof filter.plnEndFilter !== 'undefined' && filter.plnEndFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                         let val1:string = handler.getFormattedDateString(item.planEndDt);
                         let val2:string = filter.plnEndFilter;
                         return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                     });
                 }
                // filter by status
                if (typeof filter.statusFilter !== 'undefined' && filter.statusFilter.length > 0) {
                   results =  _.filter(results, function(item) {
                        let val1:string = isNil(item.tdlstatus) ? "" : item.tdlstatus;
                        let val2:string = isNil(filter.statusFilter) ? "" : filter.statusFilter;
                        return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                    });
                }
                // filter by category
                if (typeof filter.categoryFilter !== 'undefined' && filter.categoryFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                         let val1:string = isNil(item.category) ? "" : item.category;
                         let val2:string = isNil(filter.categoryFilter) ? "" : filter.categoryFilter;
                         return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                     });
                 }
                // filter by tdlType
                if (typeof filter.tdlTypeFilter !== 'undefined' && filter.tdlTypeFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                         let val1:string = isNil(item.tdlType) ? "" : item.tdlType;
                         let val2:string = isNil(filter.tdlTypeFilter) ? "" : filter.tdlTypeFilter;
                         return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                     });
                 }
                // filter by clinCode
                if (typeof filter.clinCodeFilter !== 'undefined' && filter.clinCodeFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                            let val1:string = isNil(item.clinCode) ? "" : item.clinCode;
                            let val2:string = filter.clinCodeFilter;
                            return (val1.toUpperCase().includes(val2.toUpperCase()));
                        });
                }
                // filter by plnDateRangeStartFilter
                if (!isNil(filter.plnDateRangeStartFilter) && filter.plnDateRangeStartFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                        if(isNil(item.planEndDt) || filter.plnDateRangeStartFilter==='Invalid date'){
                            return true;
                        }
                        let val1:Date = DateService.resetTime(item.planEndDt);// moment(item.planEndDt).toDate();
                        let val2:Date = DateService.resetTime(filter.plnDateRangeStartFilter);// moment(filter.plnDateRangeStartFilter).toDate();
                        let isvalid = (val1 >= val2 ) ? true : false;
                        // console.log(item.name + ': planenddt: ' + moment(val1).format('YYYY-MM-DD') + ' >= startfilter: ' + moment(val2).format('YYYY-MM-DD') + ' ' + isvalid );
                        return(isvalid);
                    });
                }
                // filter by plnDateRangeEndFilter
                if (!isNil(filter.plnDateRangeEndFilter) && filter.plnDateRangeEndFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                        if(isNil(item.planStartDt) || filter.plnDateRangeEndFilter==='Invalid date'){
                            return true;
                        }
                        let val1:Date = DateService.resetTime(item.planStartDt);//moment(item.planStartDt).toDate();
                        let val2:Date = DateService.resetTime(filter.plnDateRangeEndFilter);// moment(filter.plnDateRangeEndFilter).toDate();
                        let isvalid = (val1 <= val2 ) ? true : false;
                        // console.log(item.name + ': planstart: ' + moment(val1).format('YYYY-MM-DD') + ' <= endfilter: ' + moment(val2).format('YYYY-MM-DD') + ' ' + isvalid );
                        return(isvalid);
                    });
                }
            }
        }
        return results;
    }
// tslint:disable-next-line:eofline
}