import { AfterViewInit, Component, OnDestroy, OnInit } from '@angular/core';
import * as _ from 'lodash';
import * as moment from 'moment';
import { IMyDateModel, IMyDpOptions } from 'mydatepicker';
import { isNil } from 'ramda';
import { Subscription } from 'rxjs';
import { ShareDataService } from '../../../core/sharedata.service';
import { DateService } from '../../../shared/service/date.service';
import { TDLPlanHoursQuery } from '../../../timesheet/models/planhr-query.model';
import { TDLFilter, TDLFilterData } from '../../models/tdl-filter.model';
import { TDLVarianceView } from '../../models/tdl-variance.model';
import { TDLService } from '../../services/tdl.service';

declare var jQuery: any;

const TIMESHEET_DISPLAY_PERIOD = 56; // 7 WEEKS or 49 DAYS

@Component({
	selector: "[tdl-variance]",
	templateUrl: "tdl-variance.template.html",
	styleUrls: ["tdl-variance.style.scss"],
	providers: [TDLService]
})
export class TDLVarianceSummaryComponent
	implements OnInit, AfterViewInit, OnDestroy {
    public variancedata: TDLVarianceView[] = [];

    public busyLoadTDL: Subscription;

	public selQuery: TDLPlanHoursQuery;
	public selTDL: string;
    public selTDLID: number;
    public weekEndingModel: any = {};
    public  weekEnding: Date;
    public arrWeekstoDisplay:number[]=[];
    public numWeekstoDisplay: number = 7;

    public sortResultsBy: string = "name";
    public sortResultsOrder: string = "asc";

	public tdlfilter: TDLFilter = new TDLFilter();
	public tdlfilterdata: TDLFilterData = new TDLFilterData();

    public showErrorFlag: boolean;
    public alerts: Array<Object>;

    myDatePickerOptions: IMyDpOptions = {
        dateFormat: 'mm/dd/yyyy',
        firstDayOfWeek: 'su',
        sunHighlight: false,
        satHighlight: true,
        showClearDateBtn: false
    };

	public columns: Array<any> = [
		{
			name: "name",
			type: "input",
			filtering: { filterString: "", columnName: "name", placeholder: "" }
		},
		{
			name: "pmdisplay",
			type: "input",
			filtering: { filterString: "", columnName: "pmdisplay", placeholder: "" }
		},
		{
			name: "category",
			type: "input",
			filtering: { filterString: "", columnName: "category", placeholder: "" }
		},
		{
			name: "tdlstatus",
			type: "input",
			filtering: { filterString: "", columnName: "tdlstatus", placeholder: "" }
		}
	];


    constructor(private tdlService: TDLService,
        private shareData: ShareDataService) {
        this.arrWeekstoDisplay = [];
        for (let i = 0; i < this.numWeekstoDisplay; i++) {
          this.arrWeekstoDisplay.push(i);
        }
        this.alerts = [
            {
              type: 'warning',
              msg: '<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
            }
          ];
          this.showErrorFlag = false;
    }

	ngOnDestroy(): void {
		if (this.busyLoadTDL) {
			this.busyLoadTDL.unsubscribe();
		}
	}

	ngAfterViewInit() {
		for (let col of this.columns) {
			if (col.filtering) {
				this.cosmeticFix("#filter-" + col.name);
			}
        }
	}

	public cosmeticFix(elementID: any) {
		// The code below for cosmetic fix - Blue box now surrounds the icon on the search company text box
		let filterInput = jQuery(elementID);
		filterInput
			.focus(e => {
				jQuery(e.target)
					.closest(".input-group")
					.addClass("focus");
			})
			.focusout(e => {
				jQuery(e.target)
					.closest(".input-group")
					.removeClass("focus");
			});
	}

	ngOnInit(): void {
        this.weekEnding = this.shareData.selWeekEnding;
		this.RefreshPlanSheet();
	}

    public sortFnAvailability() {}

    public getThisSaturday(): Date {
        return DateService.getThisSaturday();
    }

	public selBreakInfo(selForeCast: TDLVarianceView, wkIndex: number) {
		this.selTDL = selForeCast.name;
		this.selTDLID = selForeCast.tdlId;
		this.selQuery = new TDLPlanHoursQuery();
		this.selQuery.tdlID = selForeCast.tdlId;
		this.selQuery.weekEnding = this.getWeekFormattedText(wkIndex);
	}

	public selTDLPlanInfo(selForeCast: TDLVarianceView) {
		this.selTDL = selForeCast.name;
		this.selTDLID = selForeCast.tdlId;
	}

	public RefreshPlanSheet() {
        if(isNil(this.weekEnding)){
            this.weekEnding = this.getPerformancePeriodStartWeekEnd(this.getThisSaturday());
        }
        this.setWeekEndingModel();
        let startwkEnding = this.getFormattedDate(this.weekEnding);
        this.loadResourceVarianceReportFromDate(startwkEnding);
	}


    public loadResourceVarianceReportFromDate(startWeekEnd){
        let handler = this;
        let tdq = new TDLPlanHoursQuery();
        tdq.weekEnding = startWeekEnd;
        tdq.popCode = "T01";

        let startweekEnding = this.getPerformancePeriodStartWeekEnd(startWeekEnd);
        let endWeekEnding = this.getPerformancePeriodEndWeekEnd(startWeekEnd);
        this.numWeekstoDisplay = DateService.getDifferenceBetweenDatesinWeeks(startweekEnding, endWeekEnding); // moment(endWeekEnding).diff(moment(startweekEnding), 'week') + 1;
        this.arrWeekstoDisplay = [];
        for (let i = 0; i < this.numWeekstoDisplay; i++) {
          this.arrWeekstoDisplay.push(i);
        }


        if(this.busyLoadTDL){
            this.busyLoadTDL.unsubscribe();
        }
        this.busyLoadTDL = this.tdlService.getTDLVarianceMatrixFromDate(tdq)
        .subscribe( data => {
            if (data) {
                handler.variancedata = data;
                if(!isNil(handler.shareData.selTDLName)){
                    if(handler.shareData.selTDLName.trim().length > 0){
                        handler.updateFilterTextBox("name", this.shareData.selTDLName);
                        handler.tdlfilter.tdlFilter = this.shareData.selTDLName;
                        handler.ApplyFilter();
                    }
                }
            }
        },
        error => {
            this.showErrorFlag = true;
            this.alerts = [];
            this.alerts.push({ type: "warning", msg: error });
        }
    );
    }

	public loadTDLVarianceReport() {
		let handler = this;
		this.busyLoadTDL = this.tdlService
			.getTDLVarianceMonthlyMatrix()
			.subscribe(
				data => {
					if (data) {
						handler.variancedata = data;
					}
				},
                error => {
                    this.showErrorFlag = true;
                    this.alerts = [];
                    this.alerts.push({ type: "warning", msg: error });
                }
            );
	}

    public refreshResourceFilterObject() {
		this.tdlfilterdata = null;
		this.tdlfilterdata = new TDLFilterData();

		this.tdlfilterdata.tdlFilter = this.tdlfilter.tdlFilter;
		this.tdlfilterdata.categoryFilter = this.tdlfilter.categoryFilter;
		this.tdlfilterdata.statusFilter = this.tdlfilter.statusFilter;
		this.tdlfilterdata.pmFilter = this.tdlfilter.pmFilter;
		// this.saveSearchCriteriaToLocalStorage();
		// this.saveSearchCriteriaToLocalStorage();
	}

	public onChangeTable(filterinfo): void {
		if (filterinfo) {
			if (filterinfo.columnName === "name") {
				this.tdlfilter.tdlFilter = filterinfo.filterString;
				this.tdlfilter.tdlFilter = this.tdlfilter.tdlFilter.trim();
			}
			if (filterinfo.columnName === "category") {
				this.tdlfilter.categoryFilter = filterinfo.filterString;
				this.tdlfilter.categoryFilter = this.tdlfilter.categoryFilter.trim();
			}
			if (filterinfo.columnName === "tdlstatus") {
				this.tdlfilter.statusFilter = filterinfo.filterString;
				this.tdlfilter.statusFilter = this.tdlfilter.statusFilter.trim();
			}
			if (filterinfo.columnName === "pmdisplay") {
				this.tdlfilter.pmFilter = filterinfo.filterString;
				this.tdlfilter.pmFilter = this.tdlfilter.pmFilter.trim();
			}
			if (filterinfo.refresh) {
				this.refreshResourceFilterObject();
			}
		}
	}

    public FilterSelectionChanged(event, filterColumnName){
        // if(filterColumnName === "teamCode"){
        //     this.wktdlresfilter.teamCodeFilter = event;
        //     this.refreshResourceFilterObject();
        // }
        // if(filterColumnName === "typeCode"){
        //     this.wktdlresfilter.typeCodeFilter = event;
        //     this.refreshResourceFilterObject();
        //   }
      }

	public ApplyFilter() {
		this.refreshResourceFilterObject();
	}

	public updateFilterTextBox(colName, colText) {
		let rowIndex = _.findIndex(this.columns, function(o) {
			return o.name && o.name.toString() === colName;
		});
		if (rowIndex !== -1) {
			let selRow = this.columns[rowIndex];
			if (selRow.filtering) {
				selRow.filtering.filterString = colText;
			}
		}
    }

    public setWeekEndingModel() {
        if (this.weekEnding) {
          this.weekEndingModel = {
            date: {
              year: this.weekEnding.getFullYear(),
              month: this.weekEnding.getMonth() + 1,
              day: this.weekEnding.getDate()
            }
          };
        }
    }

    public getFormattedDate(d:Date): string {
        return DateService.getFormattedText(d, "YYYY-MM-DD");
        // return `${d.getFullYear()}-${d.getMonth() + 1}-${d.getDate()}`;
    }


    public getSaturday(d:any): Date {
        let ind:Date = moment(d).toDate();
        let retd:Date = moment(d).day('Saturday').startOf('day').toDate();
        // console.log('tdl-variance - saturday of ' + ind.toString() + ' is ' + retd.toString() ) ;
        return retd;
    }

    public onWeekEndingDateChanged(event: IMyDateModel) {
        if (event.jsdate) {
          this.weekEnding = this.getPerformancePeriodStartWeekEnd(event.jsdate);
        } else {
          this.weekEnding = null;
        }
        this.setWeekEndingModel();
        let wkEnding:any = new Date(this.weekEndingModel.date.year, this.weekEndingModel.date.month - 1, this.weekEndingModel.date.day);
        let startwkEnding = this.getFormattedDate(wkEnding);
        this.loadResourceVarianceReportFromDate(startwkEnding);
      }

      public gotoPrevDisplayPeriod(){
        if (this.weekEnding) {
            let d: Date = DateService.getDateBySubtractingMonthsFromDate(DateService.getStartOfMonthFromGivenDate(this.weekEnding), 1);// moment(this.weekEnding).startOf('month').subtract(1, 'month').toDate();
            this.weekEnding = this.getPerformancePeriodStartWeekEnd(d);
        } else {
            this.weekEnding = null;
        }
        this.setWeekEndingModel();
        let wkEnding: any = new Date(this.weekEndingModel.date.year, this.weekEndingModel.date.month - 1, this.weekEndingModel.date.day);
        let startwkEnding = this.getFormattedDate(wkEnding);
        this.loadResourceVarianceReportFromDate(startwkEnding);
    }

    public gotoNextDisplayPeriod(){
        if (this.weekEnding) {
            let d: Date = DateService.getDateByAddingMonthsFromDate(DateService.getStartOfMonthFromGivenDate(this.weekEnding), 1); // getmoment(this.weekEnding).startOf('month').add(1, 'month').toDate();
            this.weekEnding = this.getPerformancePeriodStartWeekEnd(d);
        } else {
            this.weekEnding = null;
        }
        this.setWeekEndingModel();
        let wkEnding: any = new Date(this.weekEndingModel.date.year, this.weekEndingModel.date.month - 1, this.weekEndingModel.date.day);
        let startwkEnding = this.getFormattedDate(wkEnding);
        this.loadResourceVarianceReportFromDate(startwkEnding);
    }

    public getWeekName(index: number){
        let startWeekEnding = DateService.getThisSaturday(); // moment().day('Saturday').toDate();
        if(!isNil(this.weekEnding)){
          startWeekEnding = DateService.resetTime(this.weekEnding); // moment(this.curtimeSheet.weekEnding).toDate();
        }
        return DateService.getFormattedText(
            DateService.getDateByAddingDaysFromDate(
              DateService.getSaturday(startWeekEnding), index * 7
            ), "MM/DD"
          );
    }

    public getWeekFormattedText(index: number){
        let startWeekEnding = moment().day('Saturday').toDate();
        if(!isNil(this.weekEnding)){
            startWeekEnding = moment(this.weekEnding).toDate();
        }
        return moment(startWeekEnding).day('Saturday').add(index * 7, 'days').format("YYYY-MM-DD");
    }

    public getPlanHour(selVar: TDLVarianceView, wkIndex: number){
        let arrPlanHours = [selVar.pWk1, selVar.pWk2, selVar.pWk3, selVar.pWk4, selVar.pWk5, selVar.pWk6, selVar.pWk7, selVar.pWk8];
        let retVal: string = "";
        if(wkIndex < 8){
            retVal = isNil(arrPlanHours[wkIndex]) ? "-" : arrPlanHours[wkIndex].toString();
        }
        return retVal;
    }

    public getTotalPlanHour(selVar: TDLVarianceView){
        let arrPlanHours = [selVar.pWk1, selVar.pWk2, selVar.pWk3, selVar.pWk4, selVar.pWk5, selVar.pWk6, selVar.pWk7, selVar.pWk8];
        let retVal: string = "";
        let totalPlanHour = 0.0;
        for(let wkIndex = 0; wkIndex < this.numWeekstoDisplay; wkIndex++){
            totalPlanHour = totalPlanHour + (isNil(arrPlanHours[wkIndex]) ? 0.0 : arrPlanHours[wkIndex]);
        }
        retVal = totalPlanHour.toString();
        return retVal;
    }

    public getActualHour(selVar: TDLVarianceView, wkIndex: number){
        let arrActualHours = [selVar.aWk1, selVar.aWk2, selVar.aWk3, selVar.aWk4, selVar.aWk5, selVar.aWk6, selVar.aWk7, selVar.aWk8];
        let retVal: string = "";
        if(wkIndex < 8){
            retVal = isNil(arrActualHours[wkIndex]) ? "-" : arrActualHours[wkIndex].toString();
        }
        return retVal;
    }

    public getTotalActualHour(selVar: TDLVarianceView){
        let arrActualHours = [selVar.aWk1, selVar.aWk2, selVar.aWk3, selVar.aWk4, selVar.aWk5, selVar.aWk6, selVar.aWk7, selVar.aWk8];
        let retVal: string = "";
        let totalActualHour = 0.0;
        for(let wkIndex = 0; wkIndex < this.numWeekstoDisplay; wkIndex++){
            totalActualHour = totalActualHour + (isNil(arrActualHours[wkIndex]) ? 0.0 : arrActualHours[wkIndex]);
        }
        retVal = totalActualHour.toString();
        return retVal;
    }

    public getVarianceHour(selVar: TDLVarianceView, wkIndex: number){
        let arrPlanHours = [selVar.pWk1, selVar.pWk2, selVar.pWk3, selVar.pWk4, selVar.pWk5, selVar.pWk6, selVar.pWk7, selVar.pWk8];
        let arrActualHours = [selVar.aWk1, selVar.aWk2, selVar.aWk3, selVar.aWk4, selVar.aWk5, selVar.aWk6, selVar.aWk7, selVar.aWk8];
        let retVal: string = "";
        if(wkIndex < 8){
            if(!isNil(arrPlanHours[wkIndex]) && !isNil(arrActualHours[wkIndex]) ){
                retVal = (arrPlanHours[wkIndex] - arrActualHours[wkIndex]).toString();
            }
        }
        return retVal;
    }

    public getTotalVarianceHour(selVar: TDLVarianceView){
        let arrPlanHours = [selVar.pWk1, selVar.pWk2, selVar.pWk3, selVar.pWk4, selVar.pWk5, selVar.pWk6, selVar.pWk7, selVar.pWk8];
        let arrActualHours = [selVar.aWk1, selVar.aWk2, selVar.aWk3, selVar.aWk4, selVar.aWk5, selVar.aWk6, selVar.aWk7, selVar.aWk8];
        let retVal: string = "";
        let totalVarianceHour = 0.0;
        for(let wkIndex = 0; wkIndex < this.numWeekstoDisplay; wkIndex++){
            if(!isNil(arrPlanHours[wkIndex]) && !isNil(arrActualHours[wkIndex]) ){
                totalVarianceHour = totalVarianceHour + (arrPlanHours[wkIndex] - arrActualHours[wkIndex]);
            }
        }
        retVal = totalVarianceHour.toString();
        return retVal;
    }

    public getPerformancePeriodStartWeekEnd(selDate:Date){
        let startWeekEnding:Date = DateService.getFirstSaturdayOfMonthFromGivenDate(selDate);
        return startWeekEnding;
    }

    public getPerformancePeriodEndWeekEnd(selDate:Date){
        let endWeekEnding = DateService.getLastSaturdayOfMonthFromGivenDate(selDate);
        if(DateService.getMonthOfDate(endWeekEnding) !== DateService.getMonthOfDate(selDate)){
          endWeekEnding = DateService.getDateBySubtractingDaysFromDate(endWeekEnding, 7);
        }
        return endWeekEnding;
    }
}
