import { Component, Input, OnDestroy, OnInit, SimpleChanges, ViewChild, ViewEncapsulation } from "@angular/core";
import * as _ from "lodash";
import * as moment from 'moment';
import { BaseChartDirective } from 'ng2-charts/ng2-charts';
import { TDLBurndownHrs } from "../../models/tdl-burndown.model";
import { TDL } from "../../models/tdl.model";
import { ResourceService } from "../../services/resource.service";

@Component({
  selector: "[velocity-chart]",
  templateUrl: './velocity-chart.template.html',
  providers: [ResourceService],
  encapsulation: ViewEncapsulation.None
})
export class VelocityChartComponent implements OnInit, OnDestroy {
    @ViewChild(BaseChartDirective) _chart: BaseChartDirective;

    @Input() burndowndata: TDLBurndownHrs[];
    @Input() tdlModel: TDL;

    public tdlBurnDownHours: TDLBurndownHrs[]=[];

    public actualBurnData: number[] = [];
    public planBurnData: number[] = [];

    public sprint: String[] = [];
    public remainingHours: number;

    public velocityChartData: any[] =
    [
        {
            type: "bar",
            label: "Actual",
            backgroundColor: "rgba(54, 162, 235, 0.2)",
            data: [0],
            borderColor: "rgba(54, 162, 235, 1)",
            borderWidth: 2
            },
            {
            type: "line",
            tension: 0,
            label: "Average (Velocity)",
            fill: false,
            backgroundColor: "rgba(255,99,132, 0)",
            data: [0],
            borderColor: "rgba(255,99,132,1)",
            borderWidth: 2
            },
            {
            type: "line",
            tension: 0,
            fill: false,
            label: "Expected (Velocity)",
            backgroundColor: "rgba(255,99,132, 0)",
            data: [0],
            borderColor: "rgba(255, 206, 86, 1)"
        }
    ];
    public velocityChartLabels: String[] = ["Wk1", "Wk2"];
    public velocityChartType: String = "bar";
    public velocityChartLegend: boolean = true;

    public velocityChartOptions: any = {
        scaleShowVerticalLines: false,
        responsive: true,
        scaleOptions: {
        ticks: {
            beginAtZero: true
        }
        },
        scales: {
        yAxes: [
            {
            ticks: {
                // max: this.remainingHours,
                min: 0
            },
            display: true,
            scaleLabel: {
                display: true,
                labelString: "Hours"
            }
            }
        ]
        },
        tooltips: {
        enabled: true,
        mode: "single",
        callbacks: {
            label: function(tooltipItems, data) {
                switch (tooltipItems.datasetIndex) {
                    case 0:
                    return this.remainingHours - +tooltipItems.yLabel + " hr";
                    case 1:
                    return this.averageBurnValue + " hr / day";
                    default:
                    return this.expectedBurnValue + " hr / day";
                }
            },
            title: function(tooltipItems) {
                switch (tooltipItems[0].datasetIndex) {
                    case 0:
                    return "Actual " + "(" + tooltipItems[0].xLabel + ")";
                    case 1:
                    return "Average (Velocity)";
                    default:
                    return "Expected (Velocity)";
                }
            }
        }
        }
    };

    ngOnInit(): void {}

    ngOnDestroy(): void {}

    public getSaturday(d:any): Date {
        let ind:Date = moment(d).toDate();
        let retd:Date = moment(d).day('Saturday').startOf('day').toDate();
        // console.log('velocity-chart - saturday of ' + ind.toString() + ' is ' + retd.toString() ) ;
        return retd;
    }

    public buildweekNamesArray(from, to) {
        this.sprint = [];
        this.velocityChartLabels = [];
        let dtFrom = this.getSaturday(from);
        let dtTo = this.getSaturday(to);

        for (let d = dtFrom; d <= dtTo; d.setDate(d.getDate() + 7)) {
          let wkName = (d.getMonth() + 1).toString() + "/" + d.getDate().toString();
          this.sprint.push(wkName);
        }
    }

    public constructVelocityChartData(){
        this.actualBurnData = _.map(this.tdlBurnDownHours, 'actualHours');
        this.planBurnData = _.map(this.tdlBurnDownHours, 'planHours');

        let curDate = new Date();
        let planStartDate = (this.tdlModel.planStartDt ? this.tdlModel.planStartDt : curDate);
        let planEndDate = (this.tdlModel.planEndDt ? this.tdlModel.planEndDt : curDate.setMonth(curDate.getMonth() + 3));
        this.buildweekNamesArray(planStartDate, planEndDate);
    }

    public generateActualData() {
        let handler = this;
        return this.actualBurnData.map(function (x) {
          return handler.remainingHours - (+x);
        });
    }

    // public generateAverageData() {
    //     let sum = 0;
    //     this.actualBurnData.forEach(function (value) {
    //       sum += value;
    //     });
    //     let average = sum / this.actualBurnDays;
    //     this.averageBurnValue = average;
    //     let startWork = average > 0;
    //     let data = [];
    //     let i = 0;
    //     while (true && startWork) {
    //       data[i] = this.remainingHours - average * (i + 1);
    //       if (data[i] <= 0) break;
    //       i++;
    //     }
    //     return data;
    // }

    // public generateExpectedData() {
    //     let data = [];
    //     data[this.actualBurnDays - 1] = this.actualData[this.actualBurnDays - 1];
    //     let rate = (data[this.actualBurnDays - 1] - 0) / this.remainingDay;
    //     let expectedBurnValue = rate;
    //     for (let i = this.actualBurnDays; i < this.sprintLength - 1; i++) {
    //       data[i] = data[i - 1] - rate;
    //     }
    //     data[this.sprintLength - 1] = 0;
    //     return data;
    // }

    public generatePlannedData() {
        let handler = this;
        return this.planBurnData.map(function (x) {
          return handler.remainingHours - (+x);
        });
    }

    ngOnChanges(changes: SimpleChanges) {
        for (let propName in changes) {
            if (propName === "burndowndata") {
                let chg = changes[propName];
                if (chg.currentValue) {
                    this.tdlBurnDownHours = chg.currentValue;
                    this.constructVelocityChartData();
                }
            }
        }
    }
}
