import { Component, OnInit, ViewEncapsulation, OnDestroy, AfterViewInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { TDLView } from '../models/tdlview.model';
import { TDLService } from '../services/tdl.service';
import { Subscription } from 'rxjs';

@Component({
    selector: 'modal-content',
    templateUrl: './tdl-summary-info.template.html',
    styleUrls: ['./tdl-summary-info.style.scss'],
    providers: [TDLService],
    encapsulation: ViewEncapsulation.None
})

export class TDLSummaryInfoComponent implements OnInit, AfterViewInit, OnDestroy {
    public title: string;
    public closeBtnName: string;
    public tdl: TDLView;
    public busyLoadComments: Subscription;
    public commentsdata: any[] = [];
    public sortTDLCommentsBy = "createdDt";
	public sortTDLCommentsOrder = "desc";
	public pageNumComments: number = 1;

    constructor(public bsModalRef: BsModalRef,
        public tdlService: TDLService) {
        }

    ngOnInit() {
        this.loadComments(this.tdl.tdlID);
    }

    ngAfterViewInit() {
    }

    ngOnDestroy(){
        if(this.busyLoadComments){
            this.busyLoadComments.unsubscribe();
        }
    }

    public loadComments(tdlID: number) {
        let that = this;
        this.busyLoadComments = this.tdlService
            .getTDLComments(tdlID)
            .subscribe(
                data => {
                    if (data) {
                        that.commentsdata = data;
                    }
                },
                error => {
                }
            );
    }
}
