import {Component, ElementRef, OnInit, ViewChild, ViewEncapsulation, AfterViewInit, OnDestroy } from "@angular/core";
// import "../../../lib/dhtmlx-scheduler";
// import {} from "dhtmlxscheduler";
import { Router, ActivatedRoute } from '@angular/router';
import { GanttTaskService } from '../../../task/services/task.service';
import { GanttTask } from '../../../task/models/task';
import { CalendarEvent } from '../../../task/models/event';
import * as moment from 'moment';
import { ShareDataService } from '../../../core/sharedata.service';

declare var scheduler: any;

@Component({
    encapsulation: ViewEncapsulation.None,
    selector: "[tdl-scheduler]",
    styleUrls: ['tdl-sched.style.scss'],
    templateUrl: 'tdl-sched.template.html',
    providers: [GanttTaskService]
})
export class TDLSchedulerPage implements OnInit, AfterViewInit, OnDestroy {
    @ViewChild("tdl_calendar") schedulerContainer: ElementRef;

    public busyloadParams: any;
    public tdlID: number;
    public taskData: any;

	public tdlName: string;
	public plannedTotalHours: Number;
	public budgetTotalHours: Number;
	public tdlStatus: string;
	public planStartDt: Date;


    constructor(
        private router: Router,
        private route: ActivatedRoute,
        private taskService: GanttTaskService,
        private shareData: ShareDataService
        ){}

    ngOnInit(){

        this.getParamsForReconNavigation();
        this.tdlInfoLoad();

        let events = [
            {id:1, text:"Meeting",   start_date:"03/11/2018 14:00",end_date:"03/11/2018 17:00"},
            {id:2, text:"Conference",start_date:"03/15/2018 12:00",end_date:"03/18/2018 19:00"},
            {id:3, text:"Interview", start_date:"03/24/2018 09:00",end_date:"03/24/2018 10:00"}
            ];

        scheduler.skin = "Flat";
        scheduler.config.full_day = true;
        scheduler.init(this.schedulerContainer.nativeElement,new Date(),"month");
        // scheduler.parse(events, "json");//takes the name and format of the data source
    }

    ngAfterViewInit() {
    }

    ngOnDestroy() {
        if(this.busyloadParams){
          this.busyloadParams.unsubscribe();
        }
    }

    public loadtdlMilestones(tdlID){
		let handler = this;
		this.taskService.getAllMilestonesForTDL(tdlID).subscribe(data => {
			if (data) {
                handler.taskData = data;
                let events:CalendarEvent[]=[];
                for(let t of handler.taskData){
                    let event = new CalendarEvent();
                    event.id = t.id;
                    event.text = t.text;
                    event.start_date = moment(t.start_date).format('MM/DD/YYYY 0:00');
                    event.end_date = moment(t.start_date).add(1, 'day').format('MM/DD/YYYY 0:00');
                    events.push(event);
                }
                scheduler.clearAll();

                let calevents:any = JSON.stringify(events);
                // var events = [
                //     {id:1, text:"Meeting",   start_date:"04/11/2018 14:00",end_date:"04/11/2018 17:00"},
                //     {id:2, text:"Conference",start_date:"04/15/2018 12:00",end_date:"04/18/2018 19:00"},
                //     {id:3, text:"Interview", start_date:"04/24/2018 09:00",end_date:"04/24/2018 10:00"}
                //     ];
                scheduler.parse(calevents, "json");//takes the name and format of the data source

                scheduler.attachEvent("onEventSave",function(id,ev,is_new){
                    if (!ev.text) {
                        alert("Text must not be empty");
                        return false;
                    }
                    if (!(ev.text.length < 20)) {
                        alert("Text too small");
                        return false;
                    }
                    return true;
                })

                scheduler.attachEvent("onEventDeleted", function(id,ev){
    				// custom code
				});
				// handler.initTasks();
				// gantt.clearAll();
				// handler.taskData = data;
				// handler.getLinks(handler.tdlID);
				// handler.resmodel = data;
				// handler.tdlData = data;
			}
		},
		error => {
			// this.showErrorFlag = true;
			// this.alerts = [];
			// this.alerts.push({type: 'warning', msg: error});
		});

    }

    public tdlInfoLoad() {
          this.busyloadParams = this.route.params.subscribe(params => {
            if (params["id"]) {
              this.tdlID = Number(params["id"]);
              this.loadtdlMilestones(this.tdlID);
            }
          });
    }

	public getParamsForReconNavigation() {
		this.tdlName = this.shareData.tdlname;
		this.plannedTotalHours = this.shareData.planhours;
		this.budgetTotalHours = this.shareData.budgethours;
		this.tdlStatus = this.shareData.status;
		this.planStartDt = this.shareData.planStartDt;
	}

}