    public saveComments() {
        let that = this;
        this.commentsmodel.createdBy = this.auth.getLoggedUsername();
        this.commentsmodel.comments = this.tdlComments;
        this.commentsmodel.tdlId = this.tdl.tdlID;
        if (!isNil(this.commentsmodel.comments)) {
            this.busySaveComments = this.commentsService.updateComment(this.commentsmodel).subscribe(
                    data => {
                        if (data) {
                            that.commentsmodel.commentsId = null;
							that.commentsmodel.comments = "";
							that.router.navigate(["/app/tdl/details", that.tdl.tdlID]);
                        }
                    },
                    error => {
						that.showErrorFlag = true;
						that.alerts = [];
						that.alerts.push({ type: "warning", msg: error });
                    }
                );
        }
    }

	/**
	 * createTDL() calls backend server to create a new tdl.
	 * After a successful return the flow navigates to tdl details page where
	 * newly created tdl and permit is shown on the grid.
	 *
	 * @param {TDL} tdl
	 *
	 * @memberOf CreateTDLPage
	 */
	createTDL(tdl: TDL) {

		let that = this;

		// WE always default to 1 since Cloud Team is not using it.
		tdl.lcatGroupId = 1;

		// Since the button is not type submit button, we validate using jquery
		// Get finer control on what we want to do
		this.showErrorFlag = false;
		jQuery("#createtdl-form")
			.parsley()
			.validate();

		// Plan end date should be greater than start date
		jQuery('#pln-startdt').parsley().removeError('startdateerror', {updateClass: true});
		let deltadays = moment().diff(moment(tdl.planStartDt), 'days');
		if(deltadays > 365){
			jQuery('#pln-startdt').parsley().addError('startdateerror', {message: 'Error: start date is older than 365 days.', updateClass: true});
			return;
		}


		// Toggle to edit mode or navigate to the next screen if validation passes
		if (
			jQuery("#createtdl-form")
				.parsley()
				.isValid()
		) {
			this.busyCreate = this.tdlService.createTDL(tdl).subscribe(
				data => {
					if (data) {
						that.tdl = data;
						that.saveComments();
					}
				},
				error => {
					that.showErrorFlag = true;
					that.alerts = [];
					that.alerts.push({ type: "warning", msg: error });
				}
			);
		}
	}

