import { Component, OnInit, Input, ViewEncapsulation, OnDestroy, EventEmitter, Output, SimpleChanges } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { ReferenceURL } from '../../models/referenceurl.model';
import { ReferenceURLService } from '../../services/referurl.service';

declare var jQuery: any;

@Component({
  selector: "[edit-refer-url]",
  templateUrl: './edit-reference.template.html',
  styleUrls: ['./edit-reference.style.scss'],
  providers: [],
  encapsulation: ViewEncapsulation.None
})

export class EditReferencePopup implements OnInit, OnDestroy {
  public referenceURL:ReferenceURL = new ReferenceURL();
  public busy:Subscription;

  @Input() mode:String;
  @Input() model:ReferenceURL;
  @Output() updateReferenceURL = new EventEmitter<boolean>();

  constructor(
    private router: Router,
    private refurlService:ReferenceURLService,
    private route: ActivatedRoute) {
  }

  ngOnDestroy(): void {

  }
  ngOnInit(): void {
  }

  ngOnChanges(changes: SimpleChanges) {
    for (let propName in changes) {
      if (propName === "mode") {
        let chg = changes[propName];
        if (chg.currentValue) {
          this.mode = chg.currentValue;
        }
      }
      if (propName === "model") {
        let chg = changes[propName];
        if (chg.currentValue) {
          this.referenceURL.referenceURLId = chg.currentValue.referenceURLId;
          this.referenceURL.tdlId = chg.currentValue.tdlId;
          this.referenceURL.description = chg.currentValue.description;
          this.referenceURL.url = chg.currentValue.url;
        }
      }
    }
  }

  saveReferenceURL(){
    jQuery('#refurlform').parsley().validate();
    if (jQuery('#refurlform').parsley().isValid()) {
       this.busy = this.refurlService.createReferenceURL(this.referenceURL).subscribe(data => {
        if (data) {
          this.reset(true);
          this.updateReferenceURL.emit(true);
        }
      },
      error => {
      });
    }
  }

  reset(flag){
    jQuery("#refurlform").parsley().reset();
    jQuery("#edit-ref-url").modal("hide");
    jQuery('#edit-ref-url').on('hidden').find('input[type="text"]').val('');
  }
}