	public refreshResourceFilterObject() {
		this.tdlfilterdata = null;
		this.tdlfilterdata = new TDLFilterData();

		this.tdlfilterdata.tdlFilter = this.tdlfilter.tdlFilter;
		this.tdlfilterdata.categoryFilter = this.tdlfilter.categoryFilter;
		this.tdlfilterdata.statusFilter = this.tdlfilter.statusFilter;
		this.tdlfilterdata.pmFilter = this.tdlfilter.pmFilter;
		// this.saveSearchCriteriaToLocalStorage();
	}

	public onChangeTable(filterinfo): void {
		if (filterinfo) {
			if (filterinfo.columnName === "name") {
				this.tdlfilter.tdlFilter = filterinfo.filterString;
				this.tdlfilter.tdlFilter = this.tdlfilter.tdlFilter.trim();
			}
			if (filterinfo.columnName === "category") {
				this.tdlfilter.categoryFilter = filterinfo.filterString;
				this.tdlfilter.categoryFilter = this.tdlfilter.categoryFilter.trim();
			}
			if (filterinfo.columnName === "tdlstatus") {
				this.tdlfilter.statusFilter = filterinfo.filterString;
				this.tdlfilter.statusFilter = this.tdlfilter.statusFilter.trim();
			}
			if (filterinfo.columnName === "pmdisplay") {
				this.tdlfilter.pmFilter = filterinfo.filterString;
				this.tdlfilter.pmFilter = this.tdlfilter.pmFilter.trim();
			}
			if (filterinfo.refresh) {
				this.refreshResourceFilterObject();
			}
		}
	}

