	public getWeekFormattedText(index: number) {
		let startWeekEnding = moment()
			.day("Saturday")
			.toDate();
		if (!isNil(this.weekEnding)) {
			startWeekEnding = moment(this.weekEnding).toDate();
		}
		return moment(startWeekEnding)
			.day("Saturday")
			.add(index * 7, "days")
			.format("YYYY-MM-DD");
	}

	public getPlanHour(selVar: TDLVarianceMonthlyView, mon: number) {
		let arrPlanHours = [
			selVar.pMon1,
			selVar.pMon2,
			selVar.pMon3,
			selVar.pMon4,
			selVar.pMon5,
			selVar.pMon6,
			selVar.pMon7,
			selVar.pMon8,
			selVar.pMon9,
			selVar.pMon10,
			selVar.pMon11,
			selVar.pMon12
		];
		let retVal: string = "";
		if (mon < 13) {
			let itemIndex = _.findIndex(this.arrMonthstoDisplay, function(o) {
				return o === mon;
			});
			retVal = isNil(arrPlanHours[itemIndex])
				? "-"
				: arrPlanHours[itemIndex].toString().trim();
		}
		return retVal;
	}

