	public getVarianceHour(selVar: TDLVarianceMonthlyView, mon: number) {
		let arrPlanHours = [
			selVar.pMon1,
			selVar.pMon2,
			selVar.pMon3,
			selVar.pMon4,
			selVar.pMon5,
			selVar.pMon6,
			selVar.pMon7,
			selVar.pMon8,
			selVar.pMon9,
			selVar.pMon10,
			selVar.pMon11,
			selVar.pMon12
		];
		let arrActualHours = [
			selVar.aMon1,
			selVar.aMon2,
			selVar.aMon3,
			selVar.aMon4,
			selVar.aMon5,
			selVar.aMon6,
			selVar.aMon7,
			selVar.aMon8,
			selVar.aMon9,
			selVar.aMon10,
			selVar.aMon11,
			selVar.aMon12
		];
		let retVal: string = "";
		if (mon < 13) {
			let itemIndex = _.findIndex(this.arrMonthstoDisplay, function(o) {
				return o === mon;
			});
			if (
				!isNil(arrPlanHours[itemIndex]) &&
				!isNil(arrActualHours[itemIndex])
			) {
				retVal = (arrPlanHours[itemIndex] - arrActualHours[itemIndex])
					.toString()
					.trim();
			}
		}
		return retVal;
	}

	public setTDLVarianceBreakdown(
		selVar: TDLVarianceMonthlyView,
		mon: number
	) {
		this.shareData.selTDLName = selVar.name;
		let handler = this;
		let rowIndex = _.findIndex(this.listPoPs, function(o) {
			return o.name && o.name.toString() === handler.selpopCode;
		});
		if (rowIndex !== -1) {
			let selPop = this.listPoPs[rowIndex];
			let startYear = DateService.getYearOfDate(selPop.startDt);
			let endYear = DateService.getYearOfDate(selPop.endDt);
			if (mon >= 6 && mon <= 12) {
				this.shareData.selWeekEnding = DateService.getFirstSaturdayOfMonthFromGivenDate(
					new Date(startYear, mon - 1, 1)
				);
			} else {
				this.shareData.selWeekEnding = DateService.getFirstSaturdayOfMonthFromGivenDate(
					new Date(endYear, mon - 1, 1)
				);
			}
		}
	}

