	private getPerformancePeriodStartWeekEnd(selDate: Date) {
		let startWeekEnding: Date = DateService.getFirstSaturdayOfMonthFromGivenDate(
			selDate
		);
		return startWeekEnding;
	}

	private getPerformancePeriodEndWeekEnd(selDate: Date) {
		let endWeekEnding = DateService.getLastSaturdayOfMonthFromGivenDate(
			selDate
		);
		if (
			DateService.getMonthOfDate(endWeekEnding) !==
			DateService.getMonthOfDate(selDate)
		) {
			endWeekEnding = DateService.getDateBySubtractingDaysFromDate(
				endWeekEnding,
				7
			);
		}
		return endWeekEnding;
	}

	private s2ab(s) {
		let buf = new ArrayBuffer(s.length);
		let view = new Uint8Array(buf);
		for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xff;
		return buf;
	}

	private updateColumnTitles(ws) {
		var range = XLSX.utils.decode_range(ws["!ref"]);
		let pwkIndex = 1;
		let awkIndex = 1;
		for (var C = range.s.r; C <= range.e.r; ++C) {
			var address = XLSX.utils.encode_col(C) + "1"; // <-- first row, column number C
			if (!ws[address]) continue;
			ws[address].v = ws[address].v.toUpperCase();
			switch (ws[address].v.toUpperCase()) {
				case "PMON" + pwkIndex.toString():
					ws[address].v =
						"P: " +
						this.getMonthName(this.arrMonthstoDisplay[pwkIndex - 1]);
					pwkIndex++;
					break;

				case "AMON" + awkIndex.toString():
					ws[address].v =
						"A: " +
						this.getMonthName(this.arrMonthstoDisplay[awkIndex - 1]);
					awkIndex++;
					break;

				default:
					break;
			}
		}
	}

	public exportMonthlyVarianceReport(data: any, filename, sheetName) {
		let ws: XLSX.WorkSheet = XLSX.utils.json_to_sheet(data);
		let wb: XLSX.WorkBook = XLSX.utils.book_new();
		XLSX.utils.book_append_sheet(wb, ws, sheetName);

		this.updateColumnTitles(ws);

		/* save to file */
		let wbout: string = XLSX.write(wb, { bookType: "xlsx", type: "binary" });
		saveAs(new Blob([this.s2ab(wbout)]), filename);
	}

	public downloadMonthlyVarianceReport() {
		this.exportMonthlyVarianceReport(
			this.variancedata,
			"variance-report.xlsx",
			"Monthly"
		);
	}

