import { Injectable } from "@angular/core";
import { Subject } from "rxjs";
import { PipelineResourcesheetDocModel } from "./../models/pidoc.model";
/**
 *
 *
 * @export
 * @class PipelineResourcesheetEventService
 */
@Injectable()
export class PipelineResourcesheetEventService {
	private fileImportedSource = new Subject<PipelineResourcesheetDocModel>();

	fileImportedSourceLoaded$ = this.fileImportedSource.asObservable();

	public triggerFileImported(
		fileImportedModelArry: PipelineResourcesheetDocModel
	) {
		this.fileImportedSource.next(fileImportedModelArry);
	}
}
