import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { ChartsModule } from 'ng2-charts';
import { ModalModule } from 'ngx-bootstrap';
import { LaborCategoryService } from '../../admin/services/laborcat.service';
import { SharedModule } from '../../shared/shared.module';
import { KanbanCardService } from '../services/kanbancard.service';
import { ResourceService } from '../services/resource.service';
import { TDLService } from '../services/tdl.service';
import { TDLResourceService } from '../services/tdlresource.service';
import { TDLRoleService } from '../services/tdlrole.service';
import { SearchTDLPipe } from '../util/search-tdl-pipe';
import { TDLDataFilterPipe } from '../util/tdl-filter.pipe';
import { TDLPeriodReviewComponent } from './actual/tdl-period-review.component';
import { BurndownChartComponent } from './burndown/burndown-chart.component';
import { PlanvsActualCostChartComponent } from './burndown/planvsactual-cost.component';
import { PlanvsActualHoursChartComponent } from './burndown/planvsactual-hours.component';
import { ResourceBurndownChartComponent } from './burndown/res-burndown.component';
import { VelocityChartComponent } from './burndown/velocity-chart.component';
// Page that belonged to the module
import { CreateTDLPage } from './create-tdl.component';
import { EditTDLPopup } from './edit-tdl.component';
import { TDLPeriodForecastComponent } from './forecast/tdl-period-forecast.component';
import { KanbanCardComponent } from './kanban/card/card.component';
import { KanbanCardListComponent } from './kanban/cardlist/cardlist.component';
import { KanbanBoardComponent } from './kanban/kanban-board.component';
import { ImportPipelineResourceSheet } from './pipeline/import-pipe-sheet.component';
import { PipelineImportErrorPopup } from './pipeline/pi-import-error.popup.component';
import { PipelineProgressBarDynamicComponent } from './pipeline/pi-progressbar-modal.component';
import { ProjectQueryPopupModal } from './query/proj-query-popup.component';
import { EditReferencePopup } from './references/edit-reference.component';
import { CreateTDLResourcePopup } from './resource/create-tdl-resource.component';
import { EditTDLMultipleResourcesPopup } from './resource/edit-tdl-mult-resource.component';
import { EditTDLResourcePopup } from './resource/edit-tdl-resource.component';
import { SwapTDLResourcePopup } from './resource/swap-tdl-resource.component';
import { TDLSchedulerPage } from './scheduler/tdl-sched.component';
import { SearchCriteriaPage } from './search-criteria.component';
// tslint:disable-next-line: max-line-length
import { DeleteTDLFromGridComponent, EditTDLFromGridComponent, SearchTDLResultsComponent, TDLDetailsFromGridComponent, TDLInfoFromGridComponent } from './search-tdl-results.component';
import { TDLCommentsPopupComponent } from './tdl-comments-popup.component';
import { TDLDetailsPage } from './tdl-details.component';
import { TDLNewsFeedComponent } from './tdl-news-feed.component';
import { TDLSummaryInfoComponent } from './tdl-summary-info.component';
import { TDLVarianceMonthlySummaryComponent } from './variance/tdl-var-month.component';
import { TDLVarianceSummaryComponent } from './variance/tdl-variance.component';
import { AcquisitionService } from '../services/acquisition.service';

export const routes = [
  { path: '', component: SearchCriteriaPage, pathMatch: 'full' },
  { path: 'create', component: CreateTDLPage },
  { path: 'search', component: SearchCriteriaPage },
  { path: 'details/:id', component: TDLDetailsPage },
  { path: 'sched/:id', component: TDLSchedulerPage },
  { path: 'details', component: TDLDetailsPage },
  { path: 'kanban', component: KanbanBoardComponent },
  { path: 'forecast', component: TDLPeriodForecastComponent },
  { path: 'review', component: TDLPeriodReviewComponent },
  { path: 'variance', component: TDLVarianceSummaryComponent },
  { path: 'monthvar', component: TDLVarianceMonthlySummaryComponent }
];

@NgModule({
  imports: [CommonModule, SharedModule, ChartsModule, ModalModule.forRoot(), RouterModule.forChild(routes)],
  declarations: [CreateTDLPage, SearchCriteriaPage, SearchTDLPipe, TDLDetailsPage,
    TDLSchedulerPage, BurndownChartComponent, EditTDLMultipleResourcesPopup, KanbanBoardComponent, SwapTDLResourcePopup,
    KanbanCardListComponent, KanbanCardComponent, PlanvsActualHoursChartComponent, PlanvsActualCostChartComponent, VelocityChartComponent,
    EditTDLPopup, SearchTDLResultsComponent, EditTDLResourcePopup, CreateTDLResourcePopup, TDLDataFilterPipe, EditReferencePopup, ResourceBurndownChartComponent,
    ImportPipelineResourceSheet, PipelineImportErrorPopup, PipelineProgressBarDynamicComponent, DeleteTDLFromGridComponent, TDLDetailsFromGridComponent,
    EditTDLFromGridComponent, TDLInfoFromGridComponent, TDLSummaryInfoComponent, TDLNewsFeedComponent, TDLCommentsPopupComponent,
    // tslint:disable-next-line: max-line-length
    TDLPeriodForecastComponent, TDLPeriodReviewComponent, TDLVarianceSummaryComponent, TDLVarianceMonthlySummaryComponent, ProjectQueryPopupModal],
  providers: [TDLService, TDLResourceService, ResourceService, TDLRoleService, KanbanCardService, LaborCategoryService, AcquisitionService],
  entryComponents: [
    BurndownChartComponent,
    DeleteTDLFromGridComponent,
    TDLDetailsFromGridComponent,
    EditTDLFromGridComponent,
    TDLInfoFromGridComponent,
    TDLSummaryInfoComponent,
    TDLNewsFeedComponent,
    TDLCommentsPopupComponent
  ]
})

export class TDLModule {
  static routes = routes;
}
