import { Component, Input, OnInit, ViewEncapsulation } from "@angular/core";
import { Subscription, timer } from "rxjs";

@Component({
	selector: "[pi-progressbar]",
	templateUrl: "./pi-progressbar-modal.template.html",
	encapsulation: ViewEncapsulation.None
})
export class PipelineProgressBarDynamicComponent implements OnInit {
	public max: number = 200;
	public showWarning: boolean;
	public dynamic: number = 0;
	public type: string;
	public constIncrement: number = 10;

	@Input() maxout: boolean;

	private dynamicSubscription: Subscription;

	public constructor() {
		this.max = 200;
		this.dynamic = 0;
		this.constIncrement = 10;
	}

	ngOnInit() {
		this.max = 200;
		this.dynamic = 0;
		this.constIncrement = 10;

		this.dynamicSubscription = timer(1000, 1000).subscribe(t =>
			this.value(t)
		);
	}

	public value(value: number) {
		if (this.maxout) this.dynamic = this.max;

		if (this.dynamic >= this.max) this.dynamicSubscription.unsubscribe();

		if (this.dynamic < this.max) this.dynamic = value + this.constIncrement;
	}
}
