import { AfterViewInit, Component, EventEmitter, Input, OnDestroy, OnInit, Output, SimpleChange, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { Client } from '../../admin/models/client.model';
import { LaborCategoryGroup } from '../../admin/models/lcatgroup.model';
import { AuthService } from '../../login/services/auth.service';
import { DateService } from '../../shared/service/date.service';
import { CodeValue } from '../models/codevalue.model';
import { OfferPortfolio } from '../models/offerportfolio.model';
import { OPOfferingView } from '../models/opofferingview.model';
import { PoP } from '../models/pop.model';
import { ProjectChangeDate } from '../models/projchangedate.model';
import { Resource } from '../models/resource.model';
import { TDL } from '../models/tdl.model';
import { ResourceService } from '../services/resource.service';
import { TDLService } from '../services/tdl.service';

declare var jQuery: any;

/**
 *
 *
 * @export
 * @class EditTDLPopup
 */
@Component({
  selector: "[edit-tdl]",
  templateUrl: './edit-tdl.template.html',
  styleUrls: ["./edit-tdl.style.scss"],
  encapsulation: ViewEncapsulation.None
})
export class EditTDLPopup implements OnInit, AfterViewInit, OnDestroy {
  public showErrorFlag: boolean;
  public alerts: Array<Object>;

  public gtefield = "#pln-startdt";

  public tdlapproved: boolean = false;
  public allpmaccess: boolean = false;
  public switchFilter: number = 1;

  public tdl: TDL = new TDL();

  public pdc: ProjectChangeDate;

  public busyUpdate: Subscription;
  public busyLoadResources: Subscription;
  public busyLoadTDLStatus: Subscription;
  public busyLoadTDLTypes: Subscription;
  public busyLoadTDLCategories: Subscription;
  public busyLoadCLINs: Subscription;
  public busyLoadPops: Subscription;
  public busyLoadLCATGroups: Subscription;
  public busyLoadOfferings: Subscription;
  public busyLoadOPs: Subscription;
  public busyLoadClients: Subscription;

  public listSegments: CodeValue[] = [];
  public listResources: Resource[];
  public listStatuses: CodeValue[];
  public listTypes: CodeValue[];
  public listCategories: CodeValue[];
  public listCLINs: CodeValue[];
  public listLCATGroups: LaborCategoryGroup[];
  public listPoPs: PoP[];
  public listOfferings: OPOfferingView[];
  public listOPs: OfferPortfolio[];
  public listClients: Client[];

  public tdls: TDL[] = [];

  public initPlanStartDt: Date;
  public initPlanEndDt: Date;

  public datePlanStartDt: Date;
  public datePlanEndDt: Date;

  @Input() model: TDL;
  @Output() onTDLEdit = new EventEmitter<boolean>();

  // initialize the datetime control.
  datepickerOpts = {
    autoclose: true,
    todayBtn: "linked",
    todayHighlight: true,
    assumeNearbyYear: true,
    title: "Enter Date in MM/DD/YYYY format"
  };

  /*
     output field onTDLEdit used to raise an event from child edit tdl popup
     modal to parent page tdl details page. The event carries information as required
     from child to listening parent page.
  */

  /**
   * Creates an instance of EditTDLPopup.
   *
   * @param {Router} _router
   * @param {TDLService} tdlService
   *
   * @memberOf EditTDLPopup
   */
  constructor(
    private _router: Router,
    private resourceService: ResourceService,
    private authService: AuthService,
    private tdlService: TDLService
  ) {
    this.alerts = [
      {
        type: "warning",
        msg:
          '<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages When Create TDL Fails. e.g Duplicate EIN'
      }
    ];

    this.showErrorFlag = false;

    this.pdc = new ProjectChangeDate();

    this.listSegments.push(new CodeValue("SGPS", "GPS"));
    this.listSegments.push(new CodeValue("SCOM", "Commercial"));

    jQuery('#edit-tdl').on("hidden.bs.modal", () => {
      this.cleanupModal();
    });
  }

  ngOnInit(): void {
    this.showErrorFlag = false;
  }

  ngAfterViewInit(): void {
    this.loadResources();
    this.loadTDLStatuses();
    this.loadTDLTypes();
    this.loadTDLCategories();
    this.loadCLINs();
    this.loadPoPs();
    this.loadLaborCategoryGroups();
    this.loadOPs();
    this.loadClients();

		jQuery('.nav-pills a[href="#tab-edit-main"]').tab("show");
  }

  public hasRole(rolename): boolean {
    return this.authService.jwt_hasRole("ROLE_" + rolename);
  }

  public cleanupModal(){
  }

  public ngOnDestroy(): void {
    if (this.busyUpdate) {
      this.busyUpdate.unsubscribe();
    }
    if (this.busyLoadResources) {
      this.busyLoadResources.unsubscribe();
    }
    if (this.busyLoadLCATGroups) {
      this.busyLoadLCATGroups.unsubscribe();
    }
    if (this.busyLoadTDLStatus) {
      this.busyLoadTDLStatus.unsubscribe();
    }
    if (this.busyLoadTDLTypes) {
      this.busyLoadTDLTypes.unsubscribe();
    }
    if (this.busyLoadTDLCategories) {
      this.busyLoadTDLCategories.unsubscribe();
    }
    if (this.busyLoadCLINs) {
      this.busyLoadCLINs.unsubscribe();
    }
    if (this.busyLoadPops) {
      this.busyLoadPops.unsubscribe();
    }
    if (this.busyLoadOfferings) {
      this.busyLoadOfferings.unsubscribe();
    }
    if (this.busyLoadOPs) {
      this.busyLoadOPs.unsubscribe();
    }
		if(this.busyLoadClients){
			this.busyLoadClients.unsubscribe();
		}
  }

  public loadTDLStatuses() {
    this.busyLoadTDLStatus = this.tdlService.getTDLStatuses().subscribe(
      data => {
        if (data) {
          this.listStatuses = data;
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  public loadTDLCategories() {
    this.busyLoadTDLCategories = this.tdlService.getTDLCategories().subscribe(
      data => {
        if (data) {
          this.listCategories = data;
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  public loadLaborCategoryGroups() {
    this.busyLoadCLINs = this.tdlService.getLaborCategoryGroups().subscribe(
      data => {
        if (data) {
          this.listLCATGroups = data;
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }
  public loadTDLTypes() {
    this.busyLoadTDLTypes = this.tdlService.getTDLTypes().subscribe(
      data => {
        if (data) {
          this.listTypes = data;
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  public loadCLINs() {
    this.busyLoadCLINs = this.tdlService.getCLINs().subscribe(
      data => {
        if (data) {
          this.listCLINs = data;
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  public loadOfferings(opId: number) {
    this.busyLoadOfferings = this.tdlService
      .getListOfOfferingsForOP(opId)
      .subscribe(
        data => {
          if (data) {
            this.listOfferings = data;
          }
        },
        error => {
          this.showErrorFlag = true;
          this.alerts = [];
          this.alerts.push({ type: "warning", msg: error });
        }
      );
  }

  public loadOPs() {
    this.busyLoadOPs = this.tdlService.getOPs().subscribe(
      data => {
        if (data) {
          this.listOPs = data;
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

	public loadClients() {
		this.busyLoadClients = this.tdlService.getDropDownListOfClients().subscribe(
			data => {
				if (data) {
					this.listClients = data;
				}
			},
			error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

  public loadResources() {
    this.busyLoadResources = this.resourceService
      .getAllCDMs()
      .subscribe(
        data => {
          if (data) {
            this.listResources = data;
          }
        },
        error => {
          this.showErrorFlag = true;
          this.alerts = [];
          this.alerts.push({ type: "warning", msg: error });
        }
      );
  }

  public loadPoPs() {
    this.busyLoadPops = this.tdlService.getPoPs().subscribe(
      data => {
        if (data) {
          this.listPoPs = data;
        }
      },
      error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  public getFormattedDate(date) {
    return DateService.getFormattedText(date, "MM/DD/YYYY");
  }

  public getFormattedDateString(d: any): string {
    let dtString: string = "";
    var date = new Date(d);

    dtString = this.getFormattedDate(date);
    return dtString;
  }
  /**
   * ngOnChanges() used to capture changes on the input properties.
   *
   * @param {{ [propertyName: string]: SimpleChange }} changes
   *
   * @memberOf EditTDLPopup
   */
  ngOnChanges(changes: { [propertyName: string]: SimpleChange }) {
    for (let propName in changes) {
      if (propName === "model") {
        let chg = changes[propName];
        if (chg.currentValue) {
          this.tdl.tdlID = chg.currentValue.tdlID;

          this.tdl.name = chg.currentValue.name;
          this.tdl.pmResourceID = chg.currentValue.pmResourceID;
          this.tdl.notes = chg.currentValue.notes;
          this.tdl.wbsCode = chg.currentValue.wbsCode;
          this.tdl.planStartDt = chg.currentValue.planStartDt;
          this.tdl.planEndDt = chg.currentValue.planEndDt;
          // Don't convert this
          this.datePlanStartDt = new Date(
            this.getFormattedDate(this.tdl.planStartDt)
          );
          this.datePlanEndDt = new Date(
            this.getFormattedDate(this.tdl.planEndDt)
          );

          this.initPlanStartDt = this.datePlanStartDt;
          this.initPlanEndDt = this.datePlanEndDt;

          this.tdl.actualStartDt = chg.currentValue.actualStartDt;
          this.tdl.actualEndDt = chg.currentValue.actualEndDt;
          this.tdl.budgetCost = chg.currentValue.budgetCost;
          this.tdl.budgetHours = chg.currentValue.budgetHours;
          this.tdl.planCost = chg.currentValue.planCost;
          this.tdl.actualCost = chg.currentValue.actualCost;
          this.tdl.planHours = chg.currentValue.planHours;
          this.tdl.actualHours = chg.currentValue.actualHours;
          this.tdl.ceilingAmount = chg.currentValue.ceilingAmount;
          this.tdl.status = chg.currentValue.status;
          this.tdl.type = chg.currentValue.type;
          this.tdl.clin = chg.currentValue.clin;
          this.tdl.approved = null;
          this.tdl.allPM = null;
          this.tdl.popCode = chg.currentValue.popCode;
          this.tdl.category = chg.currentValue.category;
          this.tdl.createdDt = chg.currentValue.createdDt;
          this.tdl.createdBy = chg.currentValue.createdBy;
          this.tdl.lcatGroupId = chg.currentValue.lcatGroupId;
          this.tdl.opId = chg.currentValue.opId;
          this.tdl.offeringId = chg.currentValue.offeringId;
          this.tdl.winProbability = chg.currentValue.winProbability;
          this.tdl.clientId = chg.currentValue.clientId;
          this.tdl.citizen = chg.currentValue.citizen;
          this.tdl.headCount = chg.currentValue.headCount;

          this.loadOfferings(this.tdl.opId);
        }
      }
    }
  }

  // What changed
  // Date Changes
  //   - Scenario 1: Start Date Changed
  //   - Scenario 2: End Date Changed
  //   - Scenario 3: Both Dates Changed
  // Duration Impact
  //   - Duration Same
  //   - Duration Shrunk
  //   - Duration Expanded
  public verifyDateChangeImpact(tdl: TDL){
    let impacted: boolean = false;

    this.pdc.tdlId = tdl.tdlID;
    this.pdc.originalPlanStartDt = this.initPlanStartDt;
    this.pdc.originalPlanEndDt = this.initPlanEndDt;
    this.pdc.deltaStart = 0.0;
    this.pdc.deltaEnd = 0.0;
    this.pdc.deltaDuration = 0.0;

    if(this.datePlanStartDt !== this.initPlanStartDt){
      this.pdc.deltaStart = DateService.getDifferenceBetweenDatesinDays(this.initPlanStartDt, this.datePlanStartDt);
      console.log("Start Date Changed By " + this.pdc.deltaStart + " Days");
      impacted = true;
    } else {
      console.log("Start Date Same");
    }
    if(this.datePlanEndDt !== this.initPlanEndDt){
      this.pdc.deltaEnd = DateService.getDifferenceBetweenDatesinDays(this.initPlanEndDt, this.datePlanEndDt);
      console.log("End Date Changed By " + this.pdc.deltaEnd + " Days");
      impacted = true;
    }  else {
      console.log("End Date Same");
    }

    let beforeDuration = DateService.getDifferenceBetweenDatesinDays(this.initPlanStartDt, this.initPlanEndDt);
    let afterDuration = DateService.getDifferenceBetweenDatesinDays(this.datePlanStartDt, this.datePlanEndDt);

    if(beforeDuration === afterDuration){
      console.log("Project Duration the Same");
    }
    if(beforeDuration < afterDuration){
      this.pdc.deltaDuration = afterDuration - beforeDuration;
      console.log("Project Duration Expanded By " + this.pdc.deltaDuration + " Days");
      impacted = true;
    }
    if(beforeDuration > afterDuration){
      this.pdc.deltaDuration = afterDuration - beforeDuration;
      console.log("Project Duration Shrunk By " + this.pdc.deltaDuration + " Days");
      impacted = true;
    }

    return impacted = true;
  }


  public adjustTDLResourceHours(){
    let that = this;
    this.busyUpdate = this.tdlService.adjustHoursForProjectDateChange(this.pdc).subscribe(
      data => {
        if (data) {
            that.reset(true);
        }
      },
      error => {
        that.showErrorFlag = true;
        that.alerts = [];
        that.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  /**
   * updateTDL() calls backend server to make updates to the tdl information.
   * After a successful return an event is raised to the parent tdl details page
   * indicative of a successful tdl update and refreshing of the grid on the tdl
   * details page.
   *
   * @param {TDL} tdl
   *
   * @memberOf EditTDLPopup
   */
  updateTDL(tdl: TDL) {
    this.showErrorFlag = false;

    // Since the button is not type submit button, we validate using jquery
    // Get finer control on what we want to do
    jQuery("#edittdl-form")
      .parsley()
      .validate();

    // Plan end date should be greater than start date
    jQuery('#pln-enddt').parsley().removeError('enddateerror', {updateClass: true});
    if(this.datePlanEndDt < this.datePlanStartDt){
      jQuery('#pln-enddt').parsley().addError('enddateerror', {message: 'Error: before start date.', updateClass: true});
      return;
    }

    if (
      jQuery("#edittdl-form")
        .parsley()
        .isValid()
    ) {
      let dateimpact = this.verifyDateChangeImpact(tdl);

      tdl.lcatGroupId = 1;
      tdl.planStartDt = this.datePlanStartDt;
      tdl.planEndDt = this.datePlanEndDt;

      tdl.budgetCost = Number(tdl.budgetCost);
      tdl.budgetHours = Number(tdl.budgetHours);

      tdl.approved = null;
      tdl.allPM = null;

      let that = this;
      this.busyUpdate = this.tdlService.updateTDL(tdl).subscribe(
          data => {
            if (data) {
              that.tdl = data;
              if(dateimpact){
                // run another call to push the dates
                that.adjustTDLResourceHours();
              }else {
                that.reset(true);
              }
            }
          },
          error => {
            that.showErrorFlag = true;
            that.alerts = [];
            that.alerts.push({ type: "warning", msg: error });
          }
        );
    } else {
      this.showErrorFlag = true;
      this.alerts = [];
      this.alerts.push({
        type: "warning",
        msg: "Error Saving: Missing valid entries. Check all tabs"
      });
    }
  }

  public OnOPChanged($event) {
    this.loadOfferings(this.tdl.opId);
  }

  reset(flag) {
    jQuery("#edittdl-form")
      .parsley()
      .reset();
    // jQuery("#edit-tdl").find('input[type=text]').val('');

    // jQuery("#edit-tdl").find('#account-name').empty();
    // jQuery("#edit-tdl").find('#res-name').empty();

    jQuery("#edit-tdl").modal("hide");
    this.onTDLEdit.emit(flag);
  }
}
