	public refreshSearch(event) {
		this.executeSearch();
	}

	public refreshUserQuerySearch(event) {
		this.loadUserSavedFilters();
	}

	/**
	 * Reads all search criteria and filter selection from local storage
	 * Following user selections are stored
	 * SearchType - (TDL)
	 * KeywordSearch
	 *
	 * For Resource Search
	 *      -  ResourceStatus selection
	 *
	 * @memberOf SearchCriteriaPage
	 */
	public loadSearchCriteriaFromLocalStorage() {
		this.selectedSearchType =
			this.storage.retrieve("SearchType") != null
				? this.storage.retrieve("SearchType")
				: "TDL";

		if (this.selectedSearchType === "TDL") {
			this.keywordSearch =
				this.storage.retrieve("SearchResource") != null
					? this.storage.retrieve("SearchResource")
					: "";
		}
	}

	/**
	 * Saves all search criteria and filter selection from local storage
	 * Following user selections are stored
	 * SearchType - (REPORT or TDL)
	 * KeywordSearch
	 *
	 * For Report Search
	 *      - Month, Year and Report Status selection
	 *
	 * For Resource Search
	 *      -  ResourceStatus selection
	 *
	 * @memberOf SearchCriteriaPage
	 */
	public saveSearchCriteriaToLocalStorage() {
		this.storage.store("SearchType", this.selectedSearchType);

		if (this.selectedSearchType === "TDL") {
			this.storage.store("SearchResource", this.keywordSearch);
		}
	}

	/**
	 * Clears the saved tdl preferences from Local Storage
	 *
	 *
	 * @memberOf SearchCriteriaPage
	 */
	public clearResourcePreferencesFromLocalStorage() {
		this.storage.clear("sortTDLResultsBy");
		this.storage.clear("sortTDLResultsOrder");
	}

	public hasRole(rolename): boolean {
		return this.authService.jwt_hasRole("ROLE_" + rolename);
	}

	public loadProjectsByFilterQuery(filterQuery: any) {
		let handler = this;
		this.onResetError();

		let kfq: UserFilterQuery = new UserFilterQuery();
		kfq.filterBy = "";
		if (!isNil(filterQuery.rules)) {
			if (filterQuery.rules.length > 0) {
				kfq.filterBy = JSON.stringify(filterQuery);
			}
		}
		kfq.groupBy = "";

		this.busyLoadTDLs = this.tdlService.getTDLByUserFilterQuery(kfq)
			.subscribe(
				data => {
					if (data) {
						// handler.tdls = data;
						handler.resmodel = data;
						handler.tdlData = data;
					}
				},
				error => {
					handler.onShowError(error);
				}
			);
	}
	/**
	 *
	 *
	 * @param {String} searchtxt
	 *
	 * @memberOf SearchCriteriaPage
	 */
	public fetchResourceSearchResults(searchtxt: String) {
		searchtxt = searchtxt == null ? "" : searchtxt.toUpperCase();
		let tsc = new TDLSearchCriteria();
		tsc.searchFilter = searchtxt.trim();
		tsc.taskOrderFilter = this.taskOrderSearch.trim();

		// since the load may take long time, when user checks multiple filter criteria, we need to
		// abort earlier calls and re-initiate a new call
		if (this.busy) {
			this.busy.unsubscribe();
		}

		let handler = this;

		// Restrict search only to PM owned projects if the role is PM
		if (this.hasRole("PM")) {
			tsc.employeeID = this.authService.jwt_getEmployeeId();
		}

		this.busy = this.tdlService.getTDLBySearchCriteria(tsc).subscribe(
			data => {
				if (data) {
					handler.resmodel = data;
					handler.tdlData = data;
				}
			},
			error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

	public refreshFilter(refresh: any) {
		this.loadUserSavedFilters();
	}

	/**
	 *
	 *
	 * @param {String} searchtxt
	 *
	 * @memberOf SearchCriteriaPage
	 */
	public fetchSearchResults(searchtxt: String) {
		this.showErrorFlag = false;

		// reset the table first
		this.tdlData = null;

		if (this.selectedSearchType === "TDL") {
			this.clearResourcePreferencesFromLocalStorage();
			this.fetchResourceSearchResults(searchtxt);
		}

		this.saveSearchCriteriaToLocalStorage();
	}

	public onSaveSearch() {
		this.listSavedSearch.push(this.selSavedSearch);
	}

