	/**
	 * This cosmetic fix is to allow the blue focus highlight for the input group including
	 * the search icon on the textbox
	 * @param {*} elementID
	 *
	 * @memberOf SearchCriteriaPage
	 */
	public cosmeticFix(elementID: any) {
		// The code below for cosmetic fix - Blue box now surrounds the icon on the search tdl text box
		let searchInput = jQuery(elementID);
		searchInput
			.focus(e => {
				jQuery(e.target)
					.closest(".input-group")
					.addClass("focus");
			})
			.focusout(e => {
				jQuery(e.target)
					.closest(".input-group")
					.removeClass("focus");
			});
	}

