import { Injectable } from "@angular/core";
import { Observable, of } from "rxjs";
import { CodeValue } from '../models/codevalue.model';
import { UserSaveFilter } from "./../models/user-save-filter.model";
import { tap, catchError } from 'rxjs/operators';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { AuthService } from '../../login/services/auth.service';
import { environment } from '../../../environments/environment';

const httpOptions = {
	headers: new HttpHeaders({ "Content-Type": "application/json" })
}

/**
 *
 *
 * @export
export class PipelineResourceService {
 * @class PipelineResourceService
 */
@Injectable()
export class PipelineResourceService {
	private getresourceurl = environment.apiurl + "/api/v1/resource";
	private gettdlurl = environment.apiurl + "/api/v1/tdl";

	constructor(private http: HttpClient, private authService: AuthService) {}


	public saveUserFilter(ufe: UserSaveFilter): Observable<UserSaveFilter> {
		return this.http.post(this.getresourceurl + "/user/filter", ufe, httpOptions).pipe(
			tap(_ => this.log("saveSkills")),
			catchError(this.handleError<any>("saveSkills"))
		);
	}

	public deleteUserFilter(filterID: number): Observable<CodeValue> {
		return this.http.delete( this.getresourceurl + "/user/filter/" + filterID, httpOptions).pipe(
			tap(_ => this.log("saveSkills")),
			catchError(this.handleError<any>("saveSkills"))
		);
	}

	public getUserFilters(ufq: UserSaveFilter): Observable<UserSaveFilter[]> {
		return this.http.post( this.getresourceurl + "/user/filter/list", ufq, httpOptions).pipe(
			tap(_ => this.log("saveSkills")),
			catchError(this.handleError<any>("saveSkills"))
		);
	}

	public makeFileRequest(httpMethod: string, url: string, formData: FormData) {
		return new Promise((resolve, reject) => {
			let xhr = new XMLHttpRequest();
			xhr.open("POST", url, true);

			xhr.onreadystatechange = function() {
				if (xhr.readyState === 4) {
					if (xhr.status === 200) {
						resolve(xhr.response);
					} else {
						reject(xhr.response);
					}
				}
			};

			// Append JWT token

			let token = this.authService.jwt_getAccessToken();
			xhr.setRequestHeader("X-Authorization", "Bearer " + token);
			xhr.setRequestHeader("X-Requested-With", "XMLHttpRequest");
			xhr.send(formData);
		});
	}

	public importBenchResourceSheet(formData: FormData): Promise<any> {
		return this.makeFileRequest(
			"Post",
			this.getresourceurl + "/bench/import",
			formData
		);
	}

	public importPipelineReportSheet(formData: FormData): Promise<any> {
		return this.makeFileRequest(
			"Post",
			this.gettdlurl + "/pipeline/import",
			formData
		);
	}

	// public createPipelineReportDeck(): Observable<Blob> {
	// 	return this.http.get(this.getresourceurl + "/pipeline/ppt",  {
	// 		responseType: "blob"
	// 	  }).pipe(
	// 		tap(_ => this.log("createPipelineReportDeck")),
	// 		catchError(this.handleError<any>("createPipelineReportDeck"))
	// 	);
	// }

	/**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
	private handleError<T>(operation = "operation", result?: T) {
		return (error: any): Observable<T> => {
			// TODO: send the error to remote logging infrastructure
			console.error(error); // log to console instead

			// TODO: better job of transforming error for user consumption
			this.log("${operation} failed: ${error.message}");

			// Let the app keep running by returning an empty result.
			return of(result as T);
		};
	}

	/** Log a Attribute Service message with the MessageService */
	private log(message: string) {
		//this.messageService.add("Profile Service: ${message}");
	}
}
