import { isNil } from 'ramda';
import {Component, OnInit, Input, ViewChild} from "@angular/core";
import { Observable } from "rxjs";

import { KanbanCard } from '../model/card-info';
import { KanbanTask } from '../model/task-info';
import { KanbanCardService } from '../../../services/kanbancard.service';
import { ModalDirective } from 'ngx-bootstrap';
import { TDLView } from '../../../models/tdlview.model';
import { DateService } from '../../../../shared/service/date.service';
import { now } from 'moment';


@Component({
    selector: 'card',
    templateUrl: './card.template.html',
    styleUrls: ['./card.style.scss']
})
export class KanbanCardComponent implements OnInit {
    @ViewChild('childModal') public childModal:ModalDirective;
    @Input() item: KanbanCard;
    @Input() tdl: TDLView;

    public tasks : KanbanTask[];
    public newtaskdesc: string;


    constructor(private kanbancardService: KanbanCardService) {
        //console.log(this.item);
    }

    ngOnInit() {
        this.tasks =[];
        this.addNewTask("T01", "Kickoff", true, 0, "04/01" );
        this.addNewTask("T02", "Design Review", true, 1, "04/05" );
        // this.addNewTask("T03", "Development", false, 2, "05/05" );
        // this.addNewTask("T04", "UAT", false, 3, "05/25" );
        // this.addNewTask("T05", "Preprod", false, 4, "06/04" );
        // this.addNewTask("T06", "Deployment", false, 5, "06/09" );
        //console.log(this.item);
        // this.kanbancardService.getTasksByCardId(this.item.$key)
        //     .subscribe(data => {
        //         this.tasks = data;
        //     })
    }

    public getPercentComplete(tdl:TDLView){
        let percentComplete: any = 0.0;
        if(tdl.planCost > 0.0){
            if(!isNil(tdl.ceilingAmount && tdl.ceilingAmount > 0.0)){
                percentComplete = (tdl.actualCost)/(tdl.ceilingAmount);
            }else {
                percentComplete = (tdl.actualCost)/(tdl.planCost);
            }
        }
        return percentComplete;
    }

    public getCompletionDate(tdl:TDLView){
        return DateService.getFormattedText(tdl.planEndDt, 'MM/DD'); //moment(tdl.planEndDt).format('MM/DD');
    }

    public isPastDue(pastdue: boolean){
        if(!isNil(pastdue)){
            return pastdue;
        }
        return false;
    }

    addNewTask(key, name, iscompleted, order, completed_dt){
        let newTask = new KanbanTask();
        newTask.cardId = key;
        newTask.description = name;
        newTask.isCompleted = iscompleted;
        newTask.order = order;
        newTask.completed_dt = completed_dt;
        newTask.created_at = new Date().toString();
        this.tasks.push(newTask);
        // this.kanbancardService.addTask(newTask)
        //     .subscribe( data => {
        //         if(data){

        //         }
        //     });
    }

    deleteTask(task){
        //console.log(task);
        this.childModal.show();
    }
    public hideChildModal():void {
        this.childModal.hide();
    }

    changeTaskCompleted(task){
        //console.log(task);
        this.kanbancardService.updateTask(task.$key, task);
    }

    clickCarret(){
        this.item.isExpanded = !this.item.isExpanded;
        this.kanbancardService.updateCard(this.item.$key,this.item);
    }
}