import { Injectable } from "@angular/core";
import { GanttTask } from "../models/task";
import { Observable, of } from "rxjs";
import { CodeValue } from '../../tdl/models/codevalue.model';
import { tap, catchError } from 'rxjs/operators';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';

const httpOptions = {
	headers: new HttpHeaders({ "Content-Type": "application/json" })
}


declare var jQuery:any;

@Injectable()
export class GanttTaskService {

    private taskUrl = environment.apiurl + '/api/v1/gantt/task';

	constructor(private http: HttpClient) { }

	public insertTasks(tasks: GanttTask[]): Observable<GanttTask[]> {
		return this.http.post(this.taskUrl, tasks, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
	}

	public deleteTasks(tdlID: number): Observable<CodeValue> {
		return this.http.delete(this.taskUrl + '/tdl/' + tdlID, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
	}

	public update(id:number, task: GanttTask, tdlId: number){
		let ut:GanttTask = new GanttTask();
		ut.id = task.id;
		ut.tdlId = tdlId;
		ut.text = task.text;
		ut.start_date = task.start_date;
		ut.end_date = task.end_date;
		ut.duration = task.duration;
		ut.progress = task.progress;
		ut.parent = task.parent;
		ut.owner_id = task.owner_id;
		ut.type = task.type;
		return this.http.put(this.taskUrl + '/' + id, ut, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
	}

	public get(id){
		return this.http.get(this.taskUrl + '/' + id, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
	}

	public getAll(){
		return this.http.get(this.taskUrl, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
	}

	public getAllForTDL(tdlID){
		return this.http.get(this.taskUrl+ '/tdl/' + tdlID, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
	}

	public getAllTasksForGivenTDLs(arrTDLs:number[]){
		return this.http.post(this.taskUrl+ '/tdl/list', arrTDLs, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
	}

	public getAllMilestonesForTDL(tdlID){
		return this.http.get(this.taskUrl+ '/milestone/tdl/' + tdlID, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
	}

	public getAllForResource(resourceID){
		return this.http.get(this.taskUrl+ '/resource/' + resourceID, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
	}

	public remove(id: number){
		return this.http.delete(this.taskUrl + '/' + id, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
	}

	/**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
	private handleError<T>(operation = "operation", result?: T) {
		return (error: any): Observable<T> => {
			// TODO: send the error to remote logging infrastructure
			console.error(error); // log to console instead

			// TODO: better job of transforming error for user consumption
			this.log("${operation} failed: ${error.message}");

			// Let the app keep running by returning an empty result.
			return of(result as T);
		};
	}

	/** Log a Attribute Service message with the MessageService */
	private log(message: string) {
		//this.messageService.add("Profile Service: ${message}");
	}
}