import { AfterViewInit, Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap';
import { isNil } from 'ramda';
import { Subscription } from 'rxjs';
import { AuthService } from '../../login/services/auth.service';
import { CommentsService } from '../../shared/service/comments.service';
import { TDLComments } from '../models/tdl-comments.model';
import { TDL } from '../models/tdl.model';
import { TDLService } from '../services/tdl.service';

declare var jQuery: any;

@Component({
    selector: 'tdl-comments-popup',
    templateUrl: './tdl-comments-popup.template.html',
    styleUrls: ['./tdl-comments-popup.style.scss'],
    providers: [TDLService, CommentsService],
    encapsulation: ViewEncapsulation.None
})

export class TDLCommentsPopupComponent implements OnInit, AfterViewInit, OnDestroy {
    public title: string;
    public tdl: TDL;
    public model: TDLComments;
    public mode: string;
    public editComments: TDLComments;
    public closeBtnName: string;
    public tdlComments: string;
    public showCommentsErrorFlag: boolean;
    public busySaveComments: Subscription;
	alertsComments: Array<Object>;
    constructor(public bsModalRef: BsModalRef, private commentsService: CommentsService,
        private auth: AuthService) {
            this.alertsComments = [
                {
                    type: "warning",
                    msg:
                        '<span class="fw-semi-bold">Warning:</span> Error: Saving Comments'
                }
            ];
            this.showCommentsErrorFlag = false;
            this.model = new TDLComments();
    }

    ngOnInit() {
        if(this.mode === 'edit') {
            console.log('entering edit mode');
            this.model = this.editComments;
            this.tdlComments = this.editComments.comments;
        }
    }

    ngAfterViewInit() {
    }

    ngOnDestroy() {
        if (this.busySaveComments) {
            this.busySaveComments.unsubscribe();
        }
    }

    public saveComments() {
        let that = this;
        this.model.createdBy = this.auth.getLoggedUsername();
        this.model.comments = this.tdlComments;
        this.model.tdlId = this.tdl.tdlID;
        if (!isNil(this.model.comments)) {
            this.busySaveComments = this.commentsService.updateComment(this.model).subscribe(
                    data => {
                        if (data) {
                            that.model.commentsId = null;
                            that.model.comments = "";
                            that.reset(true);
                            // that.onCommentEdit.emit(true);
                        }
                    },
                    error => {
                        // that.showCommentsErrorFlag = true;
                        // that.alertsComments = [];
                        // that.alertsComments.push({ type: "warning", msg: error });
                    }
                );
        }
    }

    public tdlInfoRefresh(isTDLEdited: boolean, mode: string) {
    }

    public reset(flag) {
        this.bsModalRef.hide();
    }

    public cancelComments() {
        this.reset(false);
    }
}
