    public getSaturday(d:any): Date {
      let ind:Date = moment(d).toDate();
      let retd:Date = moment(d).day('Saturday').startOf('day').toDate();
      // console.log('planvsactual-cost - saturday of ' + ind.toString() + ' is ' + retd.toString() ) ;
      return retd;
    }

    public buildweekNamesArray(from, to) {
      this.sprint = [];
      this.planactChartLabels = [];
      let dtFrom = this.getSaturday(from);
      let dtTo = this.getSaturday(to);

      // for (let d = dtFrom; d <= dtTo; d.setDate(d.getDate() + 7)) {
      //   let wkName = (d.getMonth() + 1).toString() + "/" + d.getDate().toString();
      //   this.sprint.push(wkName);
      // }

      //
      for(let s of this.weekendData){
        let wkName = moment(s).format("MM/DD");
        this.sprint.push(wkName);
      }
    }

    public constructPlanvsActualChartData(){
      let curDate = new Date();
      let planStartDate = (this.tdlModel.planStartDt ? this.tdlModel.planStartDt : curDate);
      let planEndDate = (this.tdlModel.planEndDt ? this.tdlModel.planEndDt : moment(curDate).add(3, 'month').toDate());

      let arrplanBurnCostInfo = _.filter(this.tdlBurnDownInfo, function(item) {
        let isvalid: boolean = (moment(item.weekEnding).toDate() >= moment(planStartDate).toDate()) &&
        (moment(item.weekEnding).toDate() <= moment(planEndDate).toDate());
        return (isvalid);
      });

      let arractualBurnCostInfo = _.filter(this.tdlBurnDownInfo, function(item) {
        let isvalid: boolean = (moment(item.weekEnding).toDate() >= moment(planStartDate).toDate()) &&
        (moment(item.weekEnding).toDate() <= curDate);
        return (isvalid);
      });


      this.actualBurnData = _.map(arractualBurnCostInfo, 'actualCost');
      this.planBurnData = _.map(arrplanBurnCostInfo, 'planCost');
      this.weekendData = _.map(arrplanBurnCostInfo, 'weekEnding');

      this.startPlanCost = _.sum(this.planBurnData);
      this.startActualCost = _.sum(this.planBurnData);


      this.buildweekNamesArray(planStartDate, planEndDate);

      this.actualData = this.generateActualData();
      this.plannedData = this.generatePlannedData();

      this.refreshChartData(planStartDate);
    }

    public generateActualData() {
      const myClonedArray  = Object.assign([], this.actualBurnData);
      if(myClonedArray.length > 0){
        this.actualBurnData[0] = this.startPlanCost;
        for (let i = 0; i < myClonedArray.length; ++i) {
          this.actualBurnData[i+1] = this.actualBurnData[i] - myClonedArray[i];
        }
        return this.actualBurnData;
      }
    }

