import { AfterViewInit, Component, EventEmitter, Input, OnDestroy, OnInit, Output, SimpleChange, ViewEncapsulation } from "@angular/core";
import * as _ from "lodash";
import { isNil } from "ramda";
import { Subscription } from "rxjs";
import { LaborCategory } from "../../../admin/models/laborcat.model";
import { LaborCategoryService } from '../../../admin/services/laborcat.service';
import { SkillService } from "../../../resource/services/skill.service";
import { Resource } from "../../models/resource.model";
import { TDLResource } from "../../models/tdlresource.model";
import { ResourceService } from "../../services/resource.service";
import { TDLService } from "../../services/tdl.service";
import { TDLResourceService } from "../../services/tdlresource.service";
import { SwapTDLResource } from "./../../models/swaptdlresource.model";

declare var jQuery: any;

@Component({
	selector: "[swap-tdl-resource]",
	templateUrl: "./swap-tdl-resource.template.html",
	styleUrls: ["./swap-tdl-resource.style.scss"],
	providers: [TDLResourceService, LaborCategoryService, SkillService],
	encapsulation: ViewEncapsulation.None
})
export class SwapTDLResourcePopup implements OnInit, AfterViewInit, OnDestroy {
	public model = new TDLResource();

	public selTDLId: any;
	public selLCATGroupId: number;

	public listResources: Resource[];
	public laborCategories: LaborCategory[] = [];

	public currResourceId: number;

	public showErrorFlag: boolean;
	public alerts: Array<Object>;

	public busy: Subscription;
	public busySwapResource: Subscription;
	public busyloadAllSkills: Subscription;
	public busyLoadLaborCategories: Subscription;
	public busyRecalcTDLHoursAndCost: Subscription;

	public editmode: string = "Add";

	public resource: TDLResource = new TDLResource();

	@Input() TDLResources: any[];
	@Input() resModel: TDLResource;
	@Input() LCATGroupId: number;
	@Output() onResourceSwap = new EventEmitter<boolean>();

	constructor(
		private tdlResourceService: TDLResourceService,
		private resourceService: ResourceService,
		private tdlService: TDLService,
		private lcatService: LaborCategoryService
	) {
		this.alerts = [
			{
				type: "warning",
				msg:
					'<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
			}
		];

		this.showErrorFlag = false;
	}

	ngOnInit(): void {}

	ngAfterViewInit(): void {
		// this.loadLaborCategories();
		this.loadResources();
	}

	public ngOnDestroy(): void {
		if (this.busy) {
			this.busy.unsubscribe();
		}
		if (this.busySwapResource) {
			this.busySwapResource.unsubscribe();
		}
		if (this.busyRecalcTDLHoursAndCost) {
			this.busyRecalcTDLHoursAndCost.unsubscribe();
		}
		if(this.busyLoadLaborCategories){
			this.busyLoadLaborCategories.unsubscribe();
		}
	}

	ngOnChanges(changes: { [propertyName: string]: SimpleChange }) {
		for (let propName in changes) {
			if (propName === "resModel") {
				let chg = changes[propName];
				if (chg.currentValue) {
					this.resource.tdlResourceID = chg.currentValue.tdlResourceID;
					this.resource.tdlID = chg.currentValue.tdlID;
					this.resource.resourceID = chg.currentValue.resourceID;
					this.currResourceId = chg.currentValue.resourceID;
					this.resource.lcatCode = chg.currentValue.lcatCode;
				}
			}
			if (propName === "TDLResources") {
				let chg = changes[propName];
				this.TDLResources = chg.currentValue;
			}
			if(propName === "LCATGroupId"){
				let chg = changes[propName];
				this.selLCATGroupId = chg.currentValue;
				this.loadLaborCategories(this.selLCATGroupId);
			}
		}
	}

	public isResourceACurrentTDLResource(selResourceID) {
		let rowIndex = _.findIndex(this.TDLResources, function(o) {
			return o.resourceID && o.resourceID === selResourceID;
		});
		if (rowIndex !== -1) {
			return true;
		}
		return false;
	}

	public resetError() {
		this.showErrorFlag = false;
		this.alerts = [];
	}

	public ResourceChanged() {
		let resourceinTDL: boolean = false;

		this.resetError();

		let selResourceID = this.resource.resourceID;
		if (this.isResourceACurrentTDLResource(selResourceID)) {
			resourceinTDL = true;
			this.showErrorFlag = true;
			this.alerts = [];
			this.alerts.push({
				type: "warning",
				msg: "Cannot swap with an existing TDL resource"
			});
		}

		if (!isNil(selResourceID)) {
			let rowIndex = _.findIndex(this.listResources, function(o) {
				return o.resourceID && o.resourceID === selResourceID;
			});
			if (rowIndex !== -1) {
				let selRow = this.listResources[rowIndex];
				this.resource.lcatCode = selRow.lcatCode;
			}
		}
	}

	public loadLaborCategories(lcatgroupid: number) {
		if(isNil(lcatgroupid)){
			return;
		}
		this.busyLoadLaborCategories = this.lcatService.getLaborCategoryByGroup(lcatgroupid).subscribe(
			data => {
				this.laborCategories = data;
			},
			error => {
				this.showErrorFlag = true;
			}
		);
	}

	public isSwapAllowed() {
		if (this.showErrorFlag) {
			return false;
		}
		if (this.currResourceId === this.resource.resourceID) {
			return false;
		}
		return true;
	}

	public loadResources() {
		this.busy = this.resourceService.getAllActiveResources().subscribe(
			data => {
				if (data) {
					this.listResources = data;
				}
			},
			error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

	public swapTDLResource(tdlres: TDLResource): void {
		jQuery("#frm-swap-resource")
			.parsley()
			.validate();

		if (
			jQuery("#frm-swap-resource")
				.parsley()
				.isValid()
		) {
			let swapres: SwapTDLResource = new SwapTDLResource();
			swapres.tdlId = tdlres.tdlID;
			swapres.oldResourceId = this.currResourceId;
			swapres.newResourceId = tdlres.resourceID;
			swapres.newLCatCode = tdlres.lcatCode;

			let that = this;
			this.busySwapResource = this.tdlResourceService
				.swapTDLResource(swapres)
				.subscribe(
					data => {
						if (data) {
							that.recalculateTDLHoursAndCost(swapres.tdlId);
						}
					},
					error => {
						that.showErrorFlag = true;
						that.alerts = [];
						that.alerts.push({ type: "warning", msg: error });
					}
				);
		}
	}

	public recalculateTDLHoursAndCost(tdlID: number) {
		let that = this;
		this.busyRecalcTDLHoursAndCost = this.tdlService
			.recalcPlanAndActualHoursAndCost(tdlID)
			.subscribe(
				data => {
					that.reset(true);
				},
				error => {
					that.showErrorFlag = true;
					that.alerts = [];
					that.alerts.push({ type: "warning", msg: error });
				}
			);
	}

	public reset(flag) {
		jQuery("#frm-swap-resource")
			.parsley()
			.reset();
		this.showErrorFlag = false;
		jQuery("#swap-tdl-resource").modal("hide");
		jQuery("#swap-tdl-resource")
			.on("hidden")
			.find('input[type="text"]')
			.val("");
		this.onResourceSwap.emit(flag);
	}
}
