import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'SearchTDLPipe'
})

export class SearchTDLPipe implements PipeTransform {

  transform(value, args?): Array<any> {
    let searchText = new RegExp(args, 'ig');
    if (value) {
      return value.filter(history => {
        if (history.resourceName) {
          return history.resourceName.search(searchText) !== -1;
        }
      });
    }
  }
}
