import { Component, ElementRef, OnInit, ViewEncapsulation } from "@angular/core";
import { saveAs } from "file-saver";
import { Subscription } from "rxjs";
import * as XLSX from "xlsx";
import { DateService } from "../../../shared/service/date.service";
import { PipelineResourcesheetDocModel } from "../../models/pidoc.model";
import { PipelineResourceService } from "../../services/pipeline.service";
import { PipelineResourcesheetEventService } from "../../services/pipesheet-event.service";
import { AuthService } from "./../../../login/services/auth.service";
import { LocalStorageService } from 'ngx-webstorage';

declare var jQuery: any;

@Component({
	selector: "[import-pipe-sheet]",
	templateUrl: "./import-pipe-sheet.template.html",
	styleUrls: ["./import-pipe-sheet.style.scss"],
	encapsulation: ViewEncapsulation.None,
	providers: [PipelineResourceService, PipelineResourcesheetEventService]
})
export class ImportPipelineResourceSheet implements OnInit {
	filesToUpload: Array<File> = [];
	docFileName: string;
	busy: Subscription;

	entries = [];
	selectedEntry: any = {
		value: null,
		description: null
	};

	uploadInProgress: boolean = false;
	progressbarMaxOut: boolean = false;
	showImportFileErrorPopup: boolean = false;

	private _elementRef: ElementRef;

	showErrorFlag: boolean;
	alerts: any[] = [];

	constructor(
		elementRef: ElementRef,
		private resService: PipelineResourceService,
		private resSheetEventService: PipelineResourcesheetEventService,
		private authService: AuthService,
		private storage: LocalStorageService
	) {
		this._elementRef = elementRef;

		jQuery(this._elementRef.nativeElement).on("shown.bs.modal", () => {
			this.initializeModal();
		});

		jQuery(this._elementRef.nativeElement).on("hidden.bs.modal", () => {
			this.cleanupModal();
		});
	}

	ngOnInit() {
		this.resetAlerts();
		this.showErrorFlag = false;
	}

	public initializeModal() {}

	// Free up memory and stop any event listeners/hubs
	public cleanupModal() {
		this.resetAlerts();
		this.showErrorFlag = false;
		// jQuery(".fileinput").fileinput("clear");
		jQuery("#import-pipe-file")
			.find("input[type=text]")
			.val("");
		jQuery("#import-pipe-file")
			.find("input[type=radio]")
			.prop("checked", false);
		jQuery("#importfileform")
			.parsley()
			.reset();
	}

	public resetAlerts() {
		this.alerts = [];
	}

	reset() {
		jQuery("#importfileform")
			.parsley()
			.reset();
		jQuery("#import-pipe-file").modal("hide");
	}

	public cancel() {
		this.reset();
	}

	public getFilePathExtension(path) {
		let filename = path
			.split("\\")
			.pop()
			.split("/")
			.pop();
		return filename.substr(
			(Math.max(0, filename.lastIndexOf(".")) || Infinity) + 1
		);
	}

	public fileChangeEvent(event) {
		this.showErrorFlag = false;
		this.resetAlerts();

		this.filesToUpload = <Array<File>>event.target.files;
		if (!this.filesToUpload[0]) return;

		this.docFileName = this.filesToUpload[0].name;
		let ext = this.getFilePathExtension(this.docFileName);
		jQuery("#fileImport")
			.parsley()
			.removeError("Invalid-Ext");
		switch (ext.toLowerCase()) {
			case "xls":
			case "xlsx":
				break;

			default:
				let invalidexterror = "Error: Invalid file format";
				jQuery("#fileImport")
					.parsley()
					.addError("Invalid-Ext", { message: invalidexterror });
				break;
		}
	}

	public alertAdded(name: string): boolean {
		let alertPresent = false;
		this.alerts.forEach(function(item, index, array) {
			if (item.name === name) {
				alertPresent = true;
			}
		});
		return alertPresent;
	}

	public importFile() {
		jQuery("#importfileform")
			.parsley()
			.validate();

		if (
			!jQuery("#importfileform")
				.parsley()
				.isValid()
		)
			return;

		this.uploadInProgress = true;

		if (this.filesToUpload.length > 0) {
			let file: File = this.filesToUpload[0];
			let formData: FormData = new FormData();
			let docModel = new PipelineResourcesheetDocModel();
			docModel.docDesc = this.selectedEntry.value;
			docModel.tsDocument = file;
			docModel.tsFilename = file.name;
			docModel.dateFrom = DateService.getToday(); // moment().toDate();
			docModel.dateTo = DateService.getToday(); // moment().toDate();
			let loggedUsername = this.storage.retrieve('LoggedUser') ;
			formData.append("loggeduser", loggedUsername);
			formData.append(
				"dateFrom",
				DateService.getFormattedText(DateService.getToday(), "YYYY-MM-DD")
			); //moment().format('YYYY-MM-DD'));
			formData.append(
				"dateTo",
				DateService.getFormattedText(DateService.getToday(), "YYYY-MM-DD")
			); // moment().format('YYYY-MM-DD'));
			formData.append("tsDocument", file, file.name);
			this.uploadFile(formData);
		}
	}

	public onSelectionChange(entry) {
		// clone the object for immutability
		this.selectedEntry = Object.assign({}, this.selectedEntry, entry);
	}

	private s2ab(s) {
		let buf = new ArrayBuffer(s.length);
		let view = new Uint8Array(buf);
		for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xff;
		return buf;
	}

	public exportImportErrorReport(errordata: any, filename, sheetName) {
		let wserror: XLSX.WorkSheet = XLSX.utils.json_to_sheet(errordata);
		let wb: XLSX.WorkBook = XLSX.utils.book_new();
		XLSX.utils.book_append_sheet(wb, wserror, sheetName);

		/* save to file */
		let wbout: string = XLSX.write(wb, { bookType: "xlsx", type: "binary" });
		saveAs(new Blob([this.s2ab(wbout)]), filename);
	}

	public uploadFile(formData: FormData) {
		this.showErrorFlag = false;
		this.resetAlerts();

		let handler = this;
		this.resService.importPipelineReportSheet(formData).then(
			result => {
				if (result) {
					let docModel: PipelineResourcesheetDocModel = new PipelineResourcesheetDocModel();
					jQuery.extend(true, docModel, JSON.parse(result));
					handler.resSheetEventService.triggerFileImported(docModel);
					handler.progressbarMaxOut = true;
					let thisComp = this;
					setTimeout(function() {
						thisComp.uploadInProgress = false;
						thisComp.progressbarMaxOut = false;
						if (docModel.errorCount > 0) {
							handler.exportImportErrorReport(
								docModel.errorMessages,
								"import-error.xlsx",
								"error"
							);
							jQuery("#import-error-dialog").modal({
								keyboard: false,
								backdrop: "static"
							});
						} else {
							jQuery("#import-pipe-file").modal("toggle");
							jQuery("#importfileform")
								.parsley()
								.reset();
						}
					}, 3000);
				}
			},
			error => {
				this.uploadInProgress = false;
				this.progressbarMaxOut = false;
				this.showErrorFlag = true;
				this.resetAlerts();
				this.alerts.push({
					type: "warning",
					msg: "Unable to Import file. Please check file contents"
				});
				console.error(error);
			}
		);
	}

	public exportedImportErrorFile(flag) {
		this.showImportFileErrorPopup = false;
		jQuery("#import-error-dialog").modal("hide");
		jQuery("#import-pipe-file").modal("toggle");
	}
}
