	public loadLaborCategories(lcatgroupid: number) {
		if(isNil(lcatgroupid)){
			return;
		}
		this.busyLoadLaborCategories = this.lcatService.getLaborCategoryByGroup(lcatgroupid).subscribe(
			data => {
				this.laborCategories = data;
			},
			error => {
				this.showErrorFlag = true;
			}
		);
	}

	public isDuplicateLaborRateFound(tdlres: TDLResource) {
		this.lrp2Duplicate = false;
		if (
			!isNil(tdlres.period2) &&
			tdlres.period2.length > 0 &&
			!isNil(tdlres.period1) &&
			tdlres.period1.length > 0
		) {
			if (tdlres.period1 === tdlres.period2) {
				this.lrp2Duplicate = true;
				return true;
			}
		}
		return false;
	}

	public isLabourHoursWithNoLaborRatePeriodFound(tdlres: TDLResource) {
		this.lrp2Required = false;
		if (
			!isNil(tdlres.hours2) &&
			Number(tdlres.hours2) > 0 &&
			(isNil(tdlres.period2) || tdlres.period2.length == 0)
		) {
			this.lrp2Required = true;
			return true;
		}
		return false;
	}

	public createTDLResource(tdlres: TDLResource, addPlanHours: boolean): void {
		let customErrorsFound = 0;
		if (this.isLabourHoursWithNoLaborRatePeriodFound(tdlres)) {
			customErrorsFound++;
		}
		if (this.isDuplicateLaborRateFound(tdlres)) {
			customErrorsFound++;
		}

		jQuery("#edittdlresForm")
			.parsley()
			.validate();

		if (
			jQuery("#edittdlresForm")
				.parsley()
				.isValid() &&
			customErrorsFound === 0
		) {
			tdlres.tdlID = this.selTDLId;
			if (
				!isNil(tdlres.period2) &&
				tdlres.period2.length > 0 &&
				!isNil(tdlres.hours2)
			) {
				tdlres.hours2 = Number(tdlres.hours2);
			} else {
				if (!isNil(tdlres.hours2)) {
					tdlres.hours2 = 0.0;
				}
			}

			// Save the selection to command separated value
			tdlres.skills = "";
			for (let s of this.selectedskillItems) {
				tdlres.skills = tdlres.skills + s.id.toString() + ",";
			}
			if (tdlres.skills.trim().length > 0) {
				tdlres.skills = tdlres.skills.substring(
					0,
					tdlres.skills.length - 1
				);
			}

			let that = this;
			this.busy = this.tdlResourceService
				.createTDLResource(tdlres)
				.subscribe(
					savedResource => {
						if (savedResource) {
							if(addPlanHours){
								that.addPlanHoursForResource(savedResource);
							}else {
								that.recalculateTDLHoursAndCost(tdlres.tdlID);
							}
						}
					},
					error => {
						this.showErrorFlag = true;
						this.alerts = [];
						this.alerts.push({ type: "warning", msg: error });
					}
				);
		}
	}

	public addPlanHoursForResource(tdlres: TDLResource){
		let soq: SaveResourcePlanHoursQuery = new SaveResourcePlanHoursQuery();
		soq.savemethod = this.selPlanHoursMethod;
		soq.tdlId = tdlres.tdlID;
		soq.tdlResourceId = tdlres.tdlResourceID;
		soq.resourceId = tdlres.resourceID;
		soq.numberOfDays = this.durationOnProject;
		soq.offsetDays = this.startoffsetDays;
		soq.allocationPercent = this.allocationPercent/100.0;
		soq.resourceStartDate = this.datePlanStartDt;
		soq.resourceEndDate = this.datePlanEndDt;
		let that = this;
		this.busyAddResourcePlanHours = this.planSheetService
			.addResourceTDLPlanHours(soq)
			.subscribe(
				data => {
					if (data) {
						that.recalculateTDLHoursAndCost(tdlres.tdlID);
					}
				},
				error => {
					this.showErrorFlag = true;
					this.alerts = [];
					this.alerts.push({ type: "warning", msg: error });
				}
			);
	}

	public recalculateTDLHoursAndCost(tdlID: number) {
		let that = this;
		this.busyRecalcTDLHoursAndCost = this.tdlService
			.recalcPlanAndActualHoursAndCost(tdlID)
			.subscribe(
				data => {
					that.reset(true);
				},
				error => {
					this.showErrorFlag = true;
					this.alerts = [];
					this.alerts.push({ type: "warning", msg: error });
				}
			);
	}

	public reset(flag) {
		jQuery("#edittdlresForm")
			.parsley()
			.reset();
		this.showErrorFlag = false;
		jQuery("#edit-tdl-resource").modal("hide");
		jQuery("#edit-tdl-resource")
			.on("hidden")
			.find('input[type="text"]')
			.val("");
		this.onResourceEdit.emit(flag);
	}

	public unlockLaborCategory() {
		this.lcatlockFlag = !this.lcatlockFlag;
	}

