
import { Component, OnInit, OnDestroy } from "@angular/core";
import { Observable, Subscription } from "rxjs";
import { KanbanCardService } from '../../services/kanbancard.service';
import { KanbanProject } from './model/project-info';
import { KanbanCardList } from './model/cardlist-info';
import { TDLView } from '../../models/tdlview.model';
import { TDLService } from '../../services/tdl.service';
import { TDLSearchCriteria } from '../../models/tdl-search-criteria.model';
import { KanbanCard } from './model/card-info';
import * as _ from "lodash";
import { AuthService } from '../../../login/services/auth.service';
import { CodeValue } from '../../models/codevalue.model';
import { isNil } from 'ramda';
import { UserSaveFilter } from '../../models/user-save-filter.model';
import { LocalStorageService } from 'ngx-webstorage';
import { OnComponentErrorHandler } from '../../../core/coreerror.handler';
import { ResourceService } from '../../services/resource.service';
import { UserFilterQuery } from '../../models/user-filter-query.model';

@Component({
    selector: 'kanban-board',
    templateUrl: './kanban-board.template.html',
    styleUrls: ['./kanban-board.style.scss'],
    providers: [AuthService]
})
export class KanbanBoardComponent implements OnInit, OnDestroy, OnComponentErrorHandler  {
    public busyLoadTDLs: Subscription;
	public keywordSearch: string;
	public busyLoadUserFilters: Subscription;
	public busyDeleteUserFilter: Subscription;
    public title = 'TDL Kanban Board';
    public tdls: TDLView[]=[];
    public cardlists: KanbanCardList[]=[];
    public tdlgroupbyStatusArray:TDLView[][];
    public listServiceAreas: CodeValue[]=[];

	public delfiltertitle: string = "Delete Filter";
	public delfiltermessage: string =
		"Are you sure you want to delete this filter?";
	public selFilterName: string = "";
    public showErrorFlag: boolean;
    public alerts: Array<Object>;
    public selpopYear:string  = "FY20";
    public userFilters: UserSaveFilter[] = [];
    public editUserFilter: UserSaveFilter;

    constructor(private kanbanCardService: KanbanCardService,
        private storage: LocalStorageService,
        private resService: ResourceService,
        private tdlService: TDLService,
        private authService: AuthService) {
            this.listServiceAreas.push(new CodeValue("FY 2019", "FY19"));
            this.listServiceAreas.push(new CodeValue("FY 2020", "FY20"));
            this.listServiceAreas.push(new CodeValue("FY 2021", "FY21"));
            this.listServiceAreas.push(new CodeValue("FY 2022", "FY22"));
            this.listServiceAreas.push(new CodeValue("FY 2023", "FY23"));
    }

    ngOnInit(){
        this.loadAllActiveTDLProjects(this.keywordSearch);
        this.loadUserSavedFilters();
    }

    ngOnDestroy(): void {
        if(this.busyLoadTDLs){
            this.busyLoadTDLs.unsubscribe();
        }
		if (this.busyLoadUserFilters) {
			this.busyLoadUserFilters.unsubscribe();
		}
		if (this.busyDeleteUserFilter) {
			this.busyDeleteUserFilter.unsubscribe();
		}
    }

    public FilterYearChanged(){

    }


	onResetError(): void {
		this.showErrorFlag = false;
		this.alerts = [];
	}

    private getTimeStamp() {
		return " (timestamp: " + new Date().toLocaleTimeString() + ")";
    }

	onShowError(error: any): void {
		this.showErrorFlag = true;
		this.alerts = [];
		this.alerts.push({
			type: "warning",
			msg: "Error: " + error + this.getTimeStamp()
		});
    }

    onShowBodyError(error: any): void {}

    public loadCardLists(){
        this.kanbanCardService.getCardLists()
            .subscribe(c => this.cardlists = c);
    }

    public loadCards(){
        this.kanbanCardService.getCards();
    }

    public locadTasks(){
        this.kanbanCardService.getTasks();
    }

    public hasRole(rolename): boolean{
        return this.authService.jwt_hasRole("ROLE_" + rolename);
    }

    public onClickSearch(searchtxt: String) {
		this.loadAllActiveTDLProjects(searchtxt);
	}

    public loadAllActiveTDLProjects(searchtxt: String){
		searchtxt = searchtxt == null ? "" : searchtxt.toUpperCase();
        let tsc = new TDLSearchCriteria();
        tsc.searchFilter = searchtxt.trim();
        tsc.tdlCategories = "";

        let handler = this;
        if(this.hasRole("PM")){
            tsc.employeeID = this.authService.jwt_getEmployeeId();
            tsc.tdlCategories = "";
        }
        this.busyLoadTDLs = this.tdlService.getTDLBySearchCriteria(tsc)
        .subscribe( data => {
            handler.tdls = [];
            handler.tdls = data;
            handler.tdlgroupbyStatusArray = _.toArray(_.groupBy(handler.tdls, "tdlstatusCode"));
            handler.addProject("CBER CSOM");
        }, error => {
            this.showErrorFlag = true;
            this.alerts = [];
            this.alerts.push({ type: 'warning', msg: error });
        });
    }

    public addProject(name: string)
    {
        let created_at = new Date().toString();
        let newProject:KanbanProject = new KanbanProject();
        newProject.name = name;
        newProject.created_at= created_at;
        this.kanbanCardService.addProject(newProject);
        newProject.$key = "P001";

        this.cardlists = [];
        this.addCardList("Opportunities Identified", newProject.$key, "red", 1, "NEW" );
        this.addCardList("RFP in Progress", newProject.$key, "orange", 2, "APPR" );
        this.addCardList("Staffing", newProject.$key, "purple", 3, "PEND" );
        this.addCardList("In Progress", newProject.$key, "green", 4, "INPR" );
    }


    public addCard(
        name: string,
        description: string,
        isExpanded: boolean,
        cardListId: string,
        order: number
        )
    {
        let created_at = new Date().toString();
        let newCard:KanbanCard = new KanbanCard();
        newCard.name = name;
        newCard.description = description;
        newCard.cardListId = cardListId;
        newCard.isExpanded = isExpanded;
        newCard.order =  order;
        newCard.created_at = created_at;
        // this.kanabancardService.addCard(newCard);
    }

    public addCardList(
        name: string,
        projectId: string,
        color: string,
        order: number,
        listkey: string)
    {
        let created_at = new Date().toString();
        let newCardList:KanbanCardList = new KanbanCardList();
        newCardList.name = name;
        newCardList.projectId = projectId;
        newCardList.color = color;
        newCardList.order = order;
        newCardList.created_at = created_at;
        // this.kanbanCardService.addCardList(newCardList);
        newCardList.$key = listkey;

        this.cardlists.push(newCardList);
    }

    public isValidFilterSelected() {
		if (!isNil(this.selFilterName) && this.selFilterName.length > 0) {
			return true;
		}
		return false;
    }

    public refreshSearch(event) {
		this.loadUserSavedFilters();
	}

    public refreshFilter(refresh: any) {
		this.loadUserSavedFilters();
    }

    public loadKanbanProjectsByFilterQuery(filterQuery: any) {
		let handler = this;
		this.onResetError();

		let kfq: UserFilterQuery = new UserFilterQuery();
		kfq.filterBy = "";
		if (!isNil(filterQuery.rules)) {
			if (filterQuery.rules.length > 0) {
				kfq.filterBy = JSON.stringify(filterQuery);
			}
		}
        kfq.groupBy = "";
        // this.groupName = "Delivery Manager";

		// this.shareData.pipelineGroupBy = ppq.groupBy;

		this.busyLoadTDLs = this.tdlService.getTDLByUserFilterQuery(kfq)
			.subscribe(
				data => {
					if (data) {
                        handler.tdls = data;
                        handler.tdlgroupbyStatusArray = [];
                        handler.cardlists = [];
                        handler.tdlgroupbyStatusArray = _.toArray(_.groupBy(handler.tdls, "tdlstatusCode"));
                        handler.addProject("CBER CSOM");
					}
				},
				error => {
					handler.onShowError(error);
				}
			);
	}


	public setEditMode(editmode: boolean) {
		// console.log("Apply User Filter" + this.selFilterName);
		if (editmode) {
			let that = this;
			let rowIndex = _.findIndex(this.userFilters, function(o) {
				return (
					o.filterName && o.filterName.toString() === that.selFilterName
				);
			});
			if (rowIndex !== -1) {
				this.editUserFilter = Object.assign({}, this.userFilters[rowIndex]);
			}
		}
    }

    public clearFilter(){
		// this.shareData.pipelineQuery = "";
		this.storage.clear("KANBANFILTER");
		this.loadKanbanProjectsByFilterQuery("");

    }

    public loadUserSavedFilters() {
		let that = this;
		let ufq: UserSaveFilter = new UserSaveFilter();
		ufq.userName = this.authService.getLoggedUsername();
		ufq.filterCategory = "KANB";
		this.busyLoadUserFilters = this.resService.getUserFilters(ufq).subscribe(
			data => {
				if (data) {
					that.userFilters = data;
					if (that.userFilters.length > 0) {
						that.selFilterName = that.userFilters[0].filterName;
					}
					let curUserFilter = that.storage.retrieve("KANBANFILTER");
					if (!isNil(curUserFilter) && curUserFilter.length > 0) {
						that.selFilterName = curUserFilter;
						that.applyUserFilter();
					} else {
						//that.loadPipelineResources("");
					}
				}
			},
			error => {
				that.onShowError(error);
			}
		);
	}

    public deleteUserFilter() {
		// console.log("Delete User Filter" + this.selFilterName);
		let that = this;
		let rowIndex = _.findIndex(this.userFilters, function(o) {
			return o.filterName && o.filterName.toString() === that.selFilterName;
		});
		if (rowIndex !== -1) {
			let filtertoDelete: UserSaveFilter = this.userFilters[rowIndex];
			if (!isNil(filtertoDelete.userFilterID)) {
				this.busyDeleteUserFilter = this.resService
					.deleteUserFilter(filtertoDelete.userFilterID)
					.subscribe(
						data => {
							that.selFilterName = "";
							that.loadUserSavedFilters();
						},
						error => {}
					);
			}
		}
	}

    public applyUserFilter() {
		// console.log("Apply User Filter" + this.selFilterName);
		let that = this;
		let rowIndex = _.findIndex(this.userFilters, function(o) {
			return o.filterName && o.filterName.toString() === that.selFilterName;
		});
		if (rowIndex !== -1) {
			let filtertoApply: UserSaveFilter = this.userFilters[rowIndex];
			// console.log("JSON" + filtertoApply.filterJson);
			// this.shareData.pipelineQuery = filtertoApply.filterJson;
			this.loadKanbanProjectsByFilterQuery(JSON.parse(filtertoApply.filterJson));
			this.storage.store("KANBANFILTER", this.selFilterName);
		}
    }

    public changeFilterName (){

    }

}