

import { Injectable, EventEmitter, Output } from "@angular/core";
import { TDLService } from './tdl.service';
import { tap, catchError } from 'rxjs/operators';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { environment } from '../../../environments/environment';

const httpOptions = {
	headers: new HttpHeaders({ "Content-Type": "application/json" })
}

@Injectable()
export class KanbanCardService {

    private kanbanurl = environment.apiurl + '/api/v1/kanban';

    constructor(
        private http: HttpClient,
        private tdlService: TDLService
    ) {
    }

    addProject(project) {
        // return this.projects.push(project);
    }

    public getCardLists(){
        return this.http.get(this.kanbanurl + '/cardlist', httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
    }

    getCardListsById(cardListId:string){
        return this.http.get(this.kanbanurl + '/cardlist/' + cardListId, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
    }

    getCardListsByOrder(order:number) {
        return this.http.get(this.kanbanurl + '/cardlist/order/' + order, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
    }

    getCardListsByProject(projectId: string){
        return this.http.get(this.kanbanurl + '/cardlist/project/' + projectId, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
    }

    addCardList(cardlist){
        return this.http.post(this.kanbanurl + '/cardlist', cardlist, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
    }

    getCards(){
       return this.http.get(this.kanbanurl + '/cards', httpOptions).pipe(
        tap(_ => this.log("saveSkills")),
        catchError(this.handleError<any>("saveSkills"))
    );
    }

    getCardsByListId(listId:string){
        return this.http.get(this.kanbanurl + '/cards/list/' + listId, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
    }

    addCard(card){
        return this.http.post(this.kanbanurl + '/cards', card, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
    }

    updateCard(key, updCard){
        return this.http.put(this.kanbanurl + '/cards/' + key, updCard, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
    }

    getTasks(){
        return this.http.get(this.kanbanurl + '/tasks', httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
    }

    getTasksByCardId(cardId:string){
        return this.http.get(this.kanbanurl + '/tasks/' + cardId, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
    }

    addTask(task){
        return this.http.post(this.kanbanurl + '/tasks', task, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
    }

    updateTask(key, updTask){
        return this.http.put(this.kanbanurl + '/tasks/' + key, updTask, httpOptions).pipe(
            tap(_ => this.log("saveSkills")),
            catchError(this.handleError<any>("saveSkills"))
        );
    }

        /**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
	private handleError<T>(operation = "operation", result?: T) {
		return (error: any): Observable<T> => {
			// TODO: send the error to remote logging infrastructure
			console.error(error); // log to console instead

			// TODO: better job of transforming error for user consumption
			this.log("${operation} failed: ${error.message}");

			// Let the app keep running by returning an empty result.
			return of(result as T);
		};
	}

	/** Log a Attribute Service message with the MessageService */
	private log(message: string) {
		//this.messageService.add("Profile Service: ${message}");
	}
}