	public setReferenceURLMode(mode: String, selReferenceURL: ReferenceURL) {
		this.referenceURLMode = mode;
		if (mode === "ADD") {
			this.referenceURL = null;
			this.referenceURL = new ReferenceURL();
			this.referenceURL.referenceURLId = null;
			this.referenceURL.tdlId = this.tdlID;
			this.referenceURL.url = "";
			this.referenceURL.description = "";
		}
		if (mode === "EDIT") {
			this.referenceURL = null;
			this.referenceURL = Object.assign({}, selReferenceURL);
			// this.referenceURL = selReferenceURL;
		}
	}

