import { Component, Input, OnInit, SimpleChange } from "@angular/core";
import { ActivatedRoute, Router } from '@angular/router';
import { DragDropConfig, DragDropService } from 'ng2-dnd';
import { isNil } from 'ramda';
import { AuthService } from '../../../../login/services/auth.service';
import { DateService } from '../../../../shared/service/date.service';
import { TDLView } from '../../../models/tdlview.model';
import { KanbanCardService } from '../../../services/kanbancard.service';
import { KanbanCard } from '../model/card-info';
import { KanbanCardList } from '../model/cardlist-info';

@Component({
    selector: 'cardlist',
    templateUrl: './cardlist.template.html',
    styleUrls: ['./cardlist.style.scss'],
    providers: [DragDropService, DragDropConfig]
})
export class KanbanCardListComponent implements OnInit {
    @Input() item: KanbanCardList;
    @Input() grouptdl: TDLView[][];
    public cards : KanbanCard[]=[];

    public toShowAddCard:boolean;
    public editCard: KanbanCard;
    public cardname: string;
    public carddescription: string;
    public allowedDropFrom = [];
    public allowedDragTo = false;


    constructor(private kanabancardService: KanbanCardService,
        private _route: ActivatedRoute,
        private router: Router,
        private authService: AuthService) {
    }

    ngOnInit() {
 		for(let group of this.grouptdl){
            for(let tdl of group){
                let order = 1;
                if(tdl.tdlstatusCode === this.item.$key){
                    if(this.item.$key === 'INPR'){
                        let projDescription = tdl.name + "-" + tdl.tdlType + "-" + tdl.category;
                        this.addCard(projDescription, tdl.pm, false, this.item.$key, order++, this.getCompletionDate(tdl),this.getPlanHours(tdl), tdl.tdlID, this.isPastDue(tdl), this.getWinProbability(tdl));
                    }else{
                        this.addCard(tdl.name, tdl.pm, false, this.item.$key, order++, this.getCompletionDate(tdl),this.getPlanHours(tdl), tdl.tdlID, this.isPastDue(tdl), this.getWinProbability(tdl));
                    }
                }
            }
        }
        // this.kanabancardService.getCardsByListId(this.item.$key)
        //     .subscribe(data => {
        //         this.cards = data;
        //     });
        // //fill allowed drop-from containers
        // this.kanabancardService.getCardListsByOrder(this.item.order-1)
        //     .subscribe(d => {
        //             if(d.length>0)
        //                 this.allowedDropFrom.push(d[0].$key);
        //         }
        //     );
        // //fill if it has next containers
        // this.kanabancardService.getCardListsByOrder(this.item.order+1)
        //     .subscribe(d => {
        //             if(d.length>0)
        //                 this.allowedDragTo = true;
        //         }
        //     );
    }

    public hasRole(rolename): boolean{
        return this.authService.jwt_hasRole("ROLE_" + rolename);
    }

    public gotoCreateTDL(){
        this.router.navigate(['/app/tdl/create']);
    }

    public getPercentComplete(tdl:TDLView){
        let percentComplete: any = 0.0;
        if(tdl.planCost > 0.0){
            percentComplete = (tdl.actualCost)/(tdl.planCost);
        }
        return percentComplete;
    }

    public getPlanHours(tdl:TDLView){
        let planHours: any = 0.0;
        if(!isNil(tdl.planHours) && tdl.planHours > 0.0){
            planHours = (tdl.planHours);
        }
        return planHours;
    }

    public getWinProbability(tdl:TDLView){
        let winProbability: any = 0.0;
        if(!isNil(tdl.winProbability)){
            winProbability = tdl.winProbability;
        }
        return winProbability;
    }

    public getCompletionDate(tdl:TDLView){
        let startDate:string = "-";
        let endDate:string = "-";
        if(!isNil(tdl.planStartDt)){
            startDate = DateService.getFormattedText(tdl.planStartDt, 'MM/YY');
        }
        if(!isNil(tdl.planEndDt)){
            endDate = DateService.getFormattedText(tdl.planEndDt, 'MM/YY');
        }
        return startDate + "-" + endDate;
    }


    public isPastDue(tdl:TDLView){
        if(!isNil(tdl) && !isNil(tdl.tdlstatusCode)){
            if(tdl.tdlstatusCode !== 'INPR' && tdl.tdlstatusCode !== 'CLSD'){
                if(new Date(tdl.planStartDt) < new Date()){
                    return true;
                }
            }
        }
        return false;
    }

    ngOnChanges(changes: { [propertyName: string]: SimpleChange }) {
        for (let propName in changes) {
            if (propName === "grouptdl") {
                let chg = changes[propName];
                if (chg.currentValue) {
                }
            }
        }
    }

    public showAddCard(){
        this.cardname = '';
        this.carddescription = '';
        this.toShowAddCard = true;
    }

    public cancelAddCard(){
        this.toShowAddCard = false;
    }

    // public saveAddCard(){
    //     //console.log('save card');
    //     this.addCard(
    //         this.cardname,
    //         this.carddescription,
    //         true,
    //         this.item.$key,
    //     0);
    //     this.toShowAddCard = false;
    // }


    public addCard(
        name: string,
        description: string,
        isExpanded: boolean,
        cardListId: string,
        order: number,
        due: string,
        percentComplete: number,
        tdlID: number,
        pastDue: boolean,
        winProbability: number
        )
    {
        let created_at = new Date().toString();
        let newCard:KanbanCard = new KanbanCard();
        newCard.name = name;
        newCard.description = description;
        newCard.cardListId = cardListId;
        newCard.isExpanded = isExpanded;
        newCard.order =  order;
        newCard.created_at = created_at;
        newCard.due = due;
        newCard.percentComplete = percentComplete;
        newCard.tdlID = tdlID;
        newCard.pastDue = pastDue;
        newCard.winProbability = "P" + Math.round(winProbability).toString();
        this.cards.push(newCard);
        // this.kanabancardService.addCard(newCard);
    }

    public cardDropped(ev){
        let card:KanbanCard = ev.dragData;
        if(card.cardListId !== this.item.$key){
            card.cardListId = this.item.$key;
            this.kanabancardService.updateCard(card.$key, card);
        }
    }

    public allowDragFunction(card: KanbanCard){
        return this.allowedDragTo;
    }

    public allowDropFunction(): any {
        return (dragData: KanbanCard) => {
            return this.allowedDropFrom.indexOf(dragData.cardListId) > -1;
        };
    }

}