import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output, SimpleChanges, ViewEncapsulation, TemplateRef, OnChanges, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { ICellRendererAngularComp } from "ag-grid-angular";
import { GridOptions } from "ag-grid-community";
import 'ag-grid-enterprise';
import { LocalStorageService } from 'ngx-webstorage';
import { isNil } from 'ramda';
import { Subscription } from 'rxjs';
import { ShareDataService } from '../../core/sharedata.service';
import { AuthService } from '../../login/services/auth.service';
import { DateRange } from '../models/daterange.model';
import { TDL } from "../models/tdl.model";
import { TDLView } from '../models/tdlview.model';
import { TDLService } from '../services/tdl.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { TDLSummaryInfoComponent } from './tdl-summary-info.component';

declare var jQuery: any;
@Component({
    template: `<i *ngIf="params.node.group===false" class="glyphicon glyphicon-info-sign" (click)="invokeParentMethod()" title="View Project Notes"></i>`
})
export class TDLInfoFromGridComponent implements ICellRendererAngularComp {
    params: any;

    agInit(params: any): void {
        this.params = params;
    }

    public invokeParentMethod(template) {
        this.params.context.componentParent.openTDLInfoWindow(this.params.data);
    }

    refresh(params: any): boolean {
        return false;
    }
}

@Component({
    template: `<i *ngIf="params.node.group===false" class="glyphicon glyphicon-new-window" (click)="invokeParentMethod()" title="View Project Details"></i>`
})
export class TDLDetailsFromGridComponent implements ICellRendererAngularComp {
    params: any;

    agInit(params: any): void {
        this.params = params;
    }

    public invokeParentMethod() {
        this.params.context.componentParent.gotoTDLDetails(this.params.data);
    }

    refresh(params: any): boolean {
        return false;
    }
}

@Component({
    template: `<i *ngIf="params.node.group===false" class="glyphicon glyphicon-pencil" (click)="invokeParentMethod()"
    data-toggle="modal" data-keyboard="false" data-backdrop="static" data-target="#edit-tdl" title="Edit Project"
    ></i>`
})
export class EditTDLFromGridComponent implements ICellRendererAngularComp {
    params: any;

    agInit(params: any): void {
        this.params = params;
    }

    public invokeParentMethod() {
        this.params.context.componentParent.editTDL(this.params.data);
    }

    refresh(): boolean {
        return false;
    }
}


@Component({
    selector: 'delete-tdl-cell',
    template: `<i *ngIf="params.node.group===false" class="glyphicon glyphicon-trash txt-gap" (click)="invokeParentMethod()" data-toggle="modal" title = "Delete Project"
    data-keyboard="false" data-backdrop="static" data-target="#confirm-popup"></i>`,
    styles: [
        `.btn {
            line-height: 0.5
        }`
    ]
})
export class DeleteTDLFromGridComponent implements ICellRendererAngularComp {
    public params: any;

    agInit(params: any): void {
        this.params = params;
    }

    public invokeParentMethod() {
        this.params.context.componentParent.selectDeleteClient(this.params.data);
    }

    refresh(): boolean {
        return false;
    }
}

@Component({
    selector: '[search-tdl-results]',
    templateUrl: './search-tdl-results.template.html',
    styleUrls: ['./search-criteria.style.scss'],
    providers: [TDLService],
    encapsulation: ViewEncapsulation.None
})


/**
 *
 *
 * @export
 * @class SearchTDLResultsComponent
 * @implements {OnInit}
 */
export class SearchTDLResultsComponent implements OnInit, AfterViewInit, OnChanges, OnDestroy {
    public router: Router;
    public bsModalRef: BsModalRef;
    public data: any[];
    public busy: Subscription;

    public busyLoadTDL: Subscription;

    public showEditTDLFlag: boolean;

    public tdlmodel: TDL;
	public tdlviewmodel: TDLView;

    public sideBar: any;
    public gridOptions: GridOptions;
    public gridApi;
    public gridColumnApi;

    public getRowNodeId;

    public defaultColDef = {
        sortable: true,
        filter: true,
        resizable: true,
        headerClass: 'fw-semi-bold'
    };

    public showConfirmPopup: Boolean = false;
    public selectedTDL: TDLView;

    @Input() tdlResults: any;
    @Input() dateRange: DateRange;

    @Output() refreshSearch = new EventEmitter<boolean>();

    shareData: ShareDataService;

    public components;
    public rowGroupPanelShow;

    public columnDefs = [
        {
            headerName: "Edit",
            field: "value",
            cellRendererFramework: EditTDLFromGridComponent,
            colId: "edit",
            width: 75,
            filter: false,
            pinned: 'left',
            lockPinned: true
        },
        {
            headerName: "View",
            field: "value",
            cellRendererFramework: TDLDetailsFromGridComponent,
            colId: "view",
            width: 75,
            filter: false,
            pinned: 'left',
            lockPinned: true
        },
        {
            headerName: "Info",
            field: "value",
            cellRendererFramework: TDLInfoFromGridComponent,
            colId: "view",
            width: 75,
            filter: false,
            pinned: 'left',
            lockPinned: true
        },
        {
            headerName: 'Name',
            field: 'name',
            tooltipField: 'name'
        },
        {
            headerName: 'Client',
            field: 'client',
            tooltipField: 'client',
            enableRowGroup: true
        },
        {
            headerName: 'Account',
            field: 'account',
            tooltipField: 'account',
            enableRowGroup: true
        },
        {
            headerName: 'PM',
            field: 'pmdisplay',
            tooltipField: 'pmdisplay',
            enableRowGroup: true
        },
        {
            headerName: 'Headcount (est)',
            field: 'headCount',
            tooltipField: 'headCount',
            aggFunc: "sum",
            enableValue: true,
            allowedAggFuncs: ["sum"]
        },
        {
            headerName: 'Status',
            field: 'tdlstatus',
            tooltipField: 'tdlstatus',
            enableRowGroup: true
        },
        {
            headerName: 'Industry',
            field: 'industry',
            tooltipField: 'industry',
            enableRowGroup: true
        },
        {
            headerName: 'Sector',
            field: 'sector',
            tooltipField: 'sector',
            enableRowGroup: true
        },
        {
            headerName: 'Start',
            field: 'planStartDt',
            filter: 'agDateColumnFilter',
            // editable: true,
            // cellEditor: "datePicker",
            // add extra parameters for the date filter
            filterParams: {
                // provide comparator function
                comparator: function (filterLocalDateAtMidnight, cellValue) {
                    let dateAsString = cellValue;
                    if (dateAsString == null) return 0;

                    let dateParts = dateAsString.split("-");
                    let day = Number(dateParts[2]);
                    let month = Number(dateParts[1]) - 1;
                    let year = Number(dateParts[0]);
                    let cellDate = new Date(year, month, day);

                    // Now that both parameters are Date objects, we can compare
                    if (cellDate < filterLocalDateAtMidnight) {
                        return -1;
                    } else if (cellDate > filterLocalDateAtMidnight) {
                        return 1;
                    } else {
                        return 0;
                    }
                }
            }
        },
        {
            headerName: 'End',
            field: 'planEndDt',
            filter: 'agDateColumnFilter',
            // editable: true,
            // cellEditor: "datePicker",
            // add extra parameters for the date filter
            filterParams: {
                // provide comparator function
                comparator: function (filterLocalDateAtMidnight, cellValue) {
                    let dateAsString = cellValue;
                    if (dateAsString == null) return 0;

                    let dateParts = dateAsString.split("-");
                    let day = Number(dateParts[2]);
                    let month = Number(dateParts[1]) - 1;
                    let year = Number(dateParts[0]);
                    let cellDate = new Date(year, month, day);

                    // Now that both parameters are Date objects, we can compare
                    if (cellDate < filterLocalDateAtMidnight) {
                        return -1;
                    } else if (cellDate > filterLocalDateAtMidnight) {
                        return 1;
                    } else {
                        return 0;
                    }
                }
            }
        },
        {
            headerName: 'OP',
            field: 'opName',
            tooltipField: 'opName',
            enableRowGroup: true
        },
        {
            headerName: 'Offering',
            field: 'offeringName',
            tooltipField: 'offeringName',
            enableRowGroup: true
        },
        {
            headerName: 'Probability',
            field: 'winProbability',
            tooltipField: 'winProbability',
            enableRowGroup: true
        },
        {
            headerName: "Delete",
            field: "value",
            cellRendererFramework: DeleteTDLFromGridComponent,
            colId: "delete",
            width: 75,
            filter: false,
        }
    ];

    constructor(private tdlService: TDLService,
        private storage: LocalStorageService,
        router: Router,
        private _shareData: ShareDataService,
        private modalService: BsModalService,
        private authService: AuthService
    ) {
        this.shareData = _shareData;
        // this.shareData.reset();
        this.router = router;

        this.showEditTDLFlag = false;

        this.gridOptions = <GridOptions>{
            context: {
                componentParent: this
            },
            enableColResize: true,
            defaultColDef: {
                filterParams: {newRowsAction: 'keep'}
            }
        };
        this.rowGroupPanelShow = "always";
        this.getRowNodeId = function(data) {
            return data.tdlID;
          };
        // this.components = { datePicker: getDatePicker() };
        this.sideBar = {
            toolPanels: [
              {
                id: "columns",
                labelDefault: "Columns",
                labelKey: "columns",
                iconKey: "columns",
                toolPanel: "agColumnsToolPanel"
              },
              {
                id: "filters",
                labelDefault: "Filters",
                labelKey: "filters",
                iconKey: "filter",
                toolPanel: "agFiltersToolPanel"
              }
            ],
            defaultToolPanel: ""
          };
    }

    ngOnInit() {
        this.data = this.tdlResults;
        this.loadPreferences();
        this.loadSearchCriteriaFromLocalStorage();
    }

    ngAfterViewInit() {
    }

    ngOnDestroy(): void {
		if (this.busyLoadTDL) {
			this.busyLoadTDL.unsubscribe();
        }
    }

    onGridReady(params) {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    onFirstDataRendered(params){
        this.sizeToFit();
    }

     public editTDL(tdlvw: TDLView) {
        this.saveGridState("SRCH");
        let that = this;
        that.showEditTDLFlag = true;
        this.busyLoadTDL = this.tdlService.getTDL(tdlvw.tdlID)
			.subscribe(
				data => {
					if (data) {
                        that.tdlmodel = data;
					}
				},
				error => {
				}
        );
    }


    public gotoTDLDetails(tdlvw: TDLView) {
        this.saveGridState("SRCH");
        this.showEditTDLFlag = false;
        let saveRowNode = this.gridApi.getRowNode(tdlvw.tdlID);
        this.router.navigate(['/app/tdl/details/', tdlvw.tdlID]);
    }

    public cosmeticFix(elementID: any) {
        // The code below for cosmetic fix - Blue box now surrounds the icon on the search company text box
        let filterInput = jQuery(elementID);
        filterInput
            .focus((e) => {
                jQuery(e.target).closest('.input-group').addClass('focus');
            })
            .focusout((e) => {
                jQuery(e.target).closest('.input-group').removeClass('focus');
            });
    }

    public hasRole(rolename): boolean {
        return this.authService.jwt_hasRole("ROLE_" + rolename);
    }

    public loadPreferences() {
    }

    public savePreferences(tdl: TDLView) {
        this.shareData.projectManager = tdl.pm;
    }


    ngOnChanges(changes: SimpleChanges) {
        for (let propName in changes) {
            if (propName === "tdlResults") {
                this.data = changes[propName].currentValue;
                if (!isNil(this.gridApi)) {
                    this.restoreGridState("SRCH");
                    this.sizeToFit();
                    // this.gridApi.ensureIndexVisible(8, 'top');
                }
            }
        }
    }

    public getPercentComplete(tdl: TDLView) {
        let percentComplete: any = 0.0;
        if (!isNil(tdl.ceilingAmount) && (tdl.ceilingAmount > 0)) {
            percentComplete = (tdl.actualCost) / (tdl.ceilingAmount);
        } else {
            if (tdl.planCost > 0.0) {
                percentComplete = (tdl.actualCost) / (tdl.planCost);
            }
        }

        return percentComplete;
    }

    public loadSearchCriteriaFromLocalStorage() {
    }

    public isValidString(str) {
        return ((str != null) && (typeof str !== 'undefined') && (str.length >= 0));
    }

    public resetGridState() {
        this.gridColumnApi.resetColumnState();
        this.gridColumnApi.resetColumnGroupState();
        this.gridApi.setSortModel(null);
        this.gridApi.setFilterModel(null);
    }

    public restoreGridState(gridName: string) {
        let colState = this.storage.retrieve(gridName + '-colState');
        if(!isNil(colState)) {
            this.gridColumnApi.setColumnState(colState);
        }

        let groupState = this.storage.retrieve(gridName + '-groupState');
        if(!isNil(groupState)) {
            this.gridColumnApi.setColumnGroupState(groupState);
        }

        let sortState = this.storage.retrieve(gridName + '-sortState');
        if(!isNil(sortState)) {
           this.gridApi.setSortModel(sortState);
        }

        let filterState = this.storage.retrieve(gridName + '-filterState');
        if(!isNil(filterState)) {
           this.gridApi.setFilterModel(filterState);
        }
    }

    public saveGridState(gridName: string) {
        let colState = this.gridColumnApi.getColumnState();
        this.storage.store(gridName + '-colState', colState);

        let groupState = this.gridColumnApi.getColumnGroupState();
        this.storage.store(gridName + '-groupState', groupState);

        let sortState = this.gridApi.getSortModel();
        this.storage.store(gridName + '-sortState', sortState);

        let filterState = this.gridApi.getFilterModel();
        this.storage.store(gridName + '-filterState', filterState);
    }

    public setSelectedTDL(tdl: TDLView, mode: string) {
        if (mode === 'delete') {
            this.selectedTDL = tdl;
            this.showConfirmPopup = true;
        }
    }

    public deleteTDL(id) {
        this.tdlService.deleteTDLById(id).subscribe(data => {
            if (data) {
                this.refreshSearch.emit(true);
                this.showConfirmPopup = false;
                jQuery('#confirm-popup').modal('hide');
            }
        });
    }
    public sizeToFit() {
        this.gridApi.sizeColumnsToFit();
      }

    public  autoSizeAll() {
        let allColumnIds = [];
        this.gridColumnApi.getAllColumns().forEach(function(column) {
          allColumnIds.push(column.colId);
        });
        this.gridColumnApi.autoSizeColumns(allColumnIds);
      }

    public tdlInfoRefresh(isTDLEdited: boolean, mode: string){

    }

    public openTDLInfoWindow(tdlvw: TDLView){
        const initialState = {
            tdl: tdlvw,
            title: 'Project Notes'
        };
        this.bsModalRef = this.modalService.show(TDLSummaryInfoComponent, {initialState, class: 'modal-lg'});
        this.bsModalRef.content.closeBtnName = 'Close';
    }
}

function getDatePicker() {
    function Datepicker() {}
    Datepicker.prototype.init = function(params) {
      this.eInput = document.createElement("input");
      this.eInput.value = params.value;
      jQuery(this.eInput).datepicker({ dateFormat: "yyyy-mm-dd" });
    };
    Datepicker.prototype.getGui = function() {
      return this.eInput;
    };
    Datepicker.prototype.afterGuiAttached = function() {
      this.eInput.focus();
      this.eInput.select();
    };
    Datepicker.prototype.getValue = function() {
      return this.eInput.value;
    };
    Datepicker.prototype.destroy = function() {};
    Datepicker.prototype.isPopup = function() {
      return false;
    };
    return Datepicker;
  }
