import { AfterViewInit, Component, EventEmitter, Input, OnDestroy, OnInit, Output, SimpleChange, ViewEncapsulation } from "@angular/core";
import * as _ from "lodash";
import { isNil } from "ramda";
import { Subscription } from "rxjs";
import { LaborCategory } from "../../../admin/models/laborcat.model";
import { LaborCategoryService } from '../../../admin/services/laborcat.service';
import { DropDownListItem } from "../../../shared/model/dropdownlist.model";
import { Resource } from "../../models/resource.model";
import { TDLResource } from "../../models/tdlresource.model";
import { TDLRole } from "../../models/tdlrole.model";
import { ResourceService } from "../../services/resource.service";
import { TDLService } from '../../services/tdl.service';
import { TDLResourceService } from "../../services/tdlresource.service";

declare var jQuery: any;

@Component({
	selector: "[edit-tdl-mult-resource]",
	templateUrl: "./edit-tdl-mult-resource.template.html",
	styleUrls: ["./edit-tdl-mult-resource.style.scss"],
	providers: [TDLResourceService, LaborCategoryService],
	encapsulation: ViewEncapsulation.None
})
export class EditTDLMultipleResourcesPopup
	implements OnInit, AfterViewInit, OnDestroy {
	public model = new TDLResource();

	public selTDLId: any;

	public tdlRoles: TDLRole[] = [];
	public listResources: Resource[];
	public laborCategories: LaborCategory[] = [];
	public resourceId: any;
	public showErrorFlag: boolean;
	public alerts: Array<Object>;
	public activedateid: string = "active-date";
	public busy: Subscription;
	public busyRecalcTDLHoursAndCost: Subscription;
	public editmode: string = "Add";

	public dropdownList: DropDownListItem[] = [];
	public selectedItems: DropDownListItem[] = [];
	public dropdownSettings = {};

	public resource: TDLResource = new TDLResource();

	@Input() TDLId: Number;
	@Input() resModel: TDLResource;
	@Output() onResourceEdit = new EventEmitter<boolean>();

	constructor(
		private tdlResourceService: TDLResourceService,
		private resourceService: ResourceService,
		private tdlService: TDLService,
		private lcatService: LaborCategoryService
	) {
		this.alerts = [
			{
				type: "warning",
				msg:
					'<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
			}
		];

		this.showErrorFlag = false;
	}

	ngOnInit(): void {
		this.dropdownSettings = {
			singleSelection: false,
			text: "Select Resources",
			selectAllText: "Select All",
			unSelectAllText: "UnSelect All",
			enableSearchFilter: true,
			classes: "myclass custom-class"
		};
	}

	ngAfterViewInit(): void {
		this.loadLaborCategories();
		this.loadResources();
	}

	public ngOnDestroy(): void {
		if (this.busy) {
			this.busy.unsubscribe();
		}
		if (this.busyRecalcTDLHoursAndCost) {
			this.busyRecalcTDLHoursAndCost.unsubscribe();
		}
	}

	public onItemSelect(item: any) {
		// console.log(item);
		// console.log(this.selectedItems);
	}

	public OnItemDeSelect(item: any) {
		// console.log(item);
		// console.log(this.selectedItems);
	}

	public onSelectAll(items: any) {
		// console.log(items);
	}

	public onDeSelectAll(items: any) {
		// console.log(items);
	}

	ngOnChanges(changes: { [propertyName: string]: SimpleChange }) {
		for (let propName in changes) {
			if (propName === "resModel") {
				let chg = changes[propName];
				if (chg.currentValue) {
					this.resource.tdlResourceID = chg.currentValue.tdlResourceID;
					this.resource.tdlID = chg.currentValue.tdlID;
					this.resource.resourceID = chg.currentValue.resourceID;
					this.resource.lcatCode = chg.currentValue.lcatCode;
					this.resource.hours = chg.currentValue.budgetHours;
					this.resource.hours2 = chg.currentValue.budgetHours2;
					this.resource.period1 = chg.currentValue.budgetPeriod1;
					this.resource.period2 = chg.currentValue.budgetPeriod2;
					// if(this.resource.tdlResourceID > 0) {
					//   this.editmode = "Edit";
					//   jQuery('#res-name').prop('disabled', 'disabled');
					// }
				}
			}
			if (propName === "TDLId") {
				let chg = changes[propName];
				this.selTDLId = chg.currentValue;
			}
		}
	}

	public ResourceChanged() {
		let selResourceID = this.resource.resourceID;
		if (!isNil(selResourceID)) {
			let rowIndex = _.findIndex(this.listResources, function(o) {
				return o.resourceID && o.resourceID === selResourceID;
			});
			if (rowIndex !== -1) {
				let selRow = this.listResources[rowIndex];
				this.resource.lcatCode = selRow.lcatCode;
			}
		}
	}

	public loadLaborCategories() {
		this.lcatService.getAllActive().subscribe(
			data => {
				this.laborCategories = data;
			},
			error => {
				this.showErrorFlag = true;
			}
		);
	}

	loadResources() {
		let handler = this;
		this.dropdownList = [];
		this.busy = this.resourceService.getAllActiveResources().subscribe(
			data => {
				if (data) {
					handler.listResources = data;
					for (let r of handler.listResources) {
						let dli: DropDownListItem = new DropDownListItem(r.resourceID, r.name, r.lcatCode);
						handler.dropdownList.push(dli);
					}
				}
			},
			error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

	public createTDLMultipleResources(tdlres: TDLResource): void {
		tdlres.tdlID = this.selTDLId;
		this.busy = this.tdlResourceService.createTDLResource(tdlres).subscribe(
			data => {
				if (data) {
				}
			},
			error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

	public savemultipleTDLResources() {
		for (let item of this.selectedItems) {
			let tdlres: TDLResource = new TDLResource();
			tdlres.tdlID = this.selTDLId;
			tdlres.resourceID = item.id;
			tdlres.lcatCode = item.itemCode;
			tdlres.hours = 0.0;
			this.createTDLMultipleResources(tdlres);
		}
		this.recalculateTDLHoursAndCost(this.selTDLId);
	}

	public recalculateTDLHoursAndCost(tdlID: number) {
		let that = this;
		this.busyRecalcTDLHoursAndCost = this.tdlService
			.recalcPlanAndActualHoursAndCost(tdlID)
			.subscribe(
				data => {
					that.reset(true);
				},
				error => {}
			);
	}

	public reset(flag) {
		jQuery("#multresourceForm")
			.parsley()
			.reset();
		this.showErrorFlag = false;
		jQuery("#edit-tdl-mult-resource").modal("hide");
		jQuery("#edit-tdl-mult-resource")
			.on("hidden")
			.find('input[type="text"]')
			.val("");
		this.onResourceEdit.emit(flag);
	}
}
