/*
@author : Deloitte
Service class to perform tdlrole related operations.
*/

import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { environment } from '../../../environments/environment';
import { TDLRole } from '../models/tdlrole.model';

const httpOptions = {
	headers: new HttpHeaders({ "Content-Type": "application/json" })
}



/**
 *
 *
 * @export
 * @class TDLRoleService
 */
@Injectable()
export class TDLRoleService {

    private getresourceurl = environment.apiurl + '/api/v1/tdlrole';

    constructor(private http: HttpClient) { }

    /**
     *
     *
     * @param {TDLRole} tdlrole
     * @returns {Observable<TDLRole>}
     *
     * @memberOf TDLRoleService
     */
    createTDLRole(tdlrole: TDLRole): Observable<TDLRole> {
        return this.http.post(this.getresourceurl, tdlrole, httpOptions).pipe(
    			tap(_ => this.log("saveSkills")),
    			catchError(this.handleError<any>("saveSkills"))
    		);
    }

    /**
     *
     *
     * @param {TDLRole} tdlrole
     * @returns {Observable<TDLRole>}
     *
     * @memberOf TDLRoleService
     */
    updateTDLRole(tdlrole: TDLRole): Observable<TDLRole> {
        return this.http.put(this.getresourceurl, tdlrole, httpOptions).pipe(
    			tap(_ => this.log("saveSkills")),
    			catchError(this.handleError<any>("saveSkills"))
    		);
    }

    /**
     *
     *
     * @param {any} id
     * @returns
     *
     * @memberOf TDLRoleService
     */
    getTDLRole(id) {
        return this.http.get(this.getresourceurl + '/' + id, httpOptions).pipe(
			tap(_ => this.log("saveSkills")),
			catchError(this.handleError<any>("saveSkills"))
		);
    }

    deleteTDLRoleById(id){
        return this.http.delete(this.getresourceurl + '/' + id, httpOptions).pipe(
			tap(_ => this.log("saveSkills")),
			catchError(this.handleError<any>("saveSkills"))
		);
    }

    /**
     *
     *
     * @param {resourceId} resourceId
     * @returns
     *
     * @memberOf TDLRoleService
     */
    getAll(){
        return this.http.get(this.getresourceurl, httpOptions).pipe(
			tap(_ => this.log("saveSkills")),
			catchError(this.handleError<any>("saveSkills"))
		);
    }

    /**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
	private handleError<T>(operation = "operation", result?: T) {
		return (error: any): Observable<T> => {
			// TODO: send the error to remote logging infrastructure
			console.error(error); // log to console instead

			// TODO: better job of transforming error for user consumption
			this.log("${operation} failed: ${error.message}");

			// Let the app keep running by returning an empty result.
			return of(result as T);
		};
	}

	/** Log a Attribute Service message with the MessageService */
	private log(message: string) {
		//this.messageService.add("Profile Service: ${message}");
	}

}